<template>
  <div id="login">
    <div class="main-title">
      <div class="beg-login-box">
          <header><h1>GTJ-ADMIN后台登录</h1></header>
          <div class="beg-login-main">
            <div class="login-form">
              <el-form :model="ruleForm2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
                <el-form-item prop="username">
                  <i class="icon iconfont icon-yonghuming"></i><el-input type="text" v-model="ruleForm2.username" auto-complete="off" placeholder="请输入用户名"></el-input>
                </el-form-item>
                <el-form-item prop="password">
                  <i class="icon iconfont icon-mima"></i><el-input type="password" v-model="ruleForm2.password" auto-complete="off" @keyup.enter.native="submitForm" placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm">登录</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <footer><p>GTJ-ADMIN © </p></footer>
      </div>
    </div>
  </div>
</template>

<script>
import {login, sysInfo} from '@/api'
import {setToken} from '@/utils/auth'
import {menu} from '@/router'
export default {
  name: 'Login',
  data () {
    return {
      ruleForm2: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    async submitForm () {
      const res = await login(this.ruleForm2)
      if (res.result === '1') {
        setToken(res.datas.token)
        this._sysInfo()
      }
    },
    async _sysInfo () {
      const res = await sysInfo()
      if (res.result === '0') {
        sessionStorage.setItem('userInfo', JSON.stringify(res.datas.userInfo))
        res.datas.oneMenuList.map((value1) => {
          value1.twoMenuList.map((value2) => {
            menu.map((item) => {
              if (item.path === value1.menuUrl) {
                item.hidden = false
                item.meta.title = value1.menuName
                item.children.map((value) => {
                  if (value2.menuUrl === value.path) {
                    value.meta.titel = value2.menuName
                    value.hidden = false
                  }
                })
              }
            })
          })
        })
        this.$store.commit('ROUTER_MENU', menu)
        this.$router.push('/UserList')
      }
    }
  },
  created () {
    menu.map((item) => {
      item.hidden = true
      item.children.map((value) => {
        value.hidden = true
      })
    })
  }
}
</script>

<style lang="less" scoped>
.main-title {
  position: absolute;
  margin: 0;
  padding: 0;
  color: #333;
  text-align: center;
  top: 30%;
  left: 50%;
  -webkit-transform: translate3d(-50%,-30%,0);
  transform: translate3d(-50%,-30%,0);
  .beg-login-box{
    width: 450px;
    margin: 10% auto;
    padding-bottom: 30px;
    background-color: rgba(255, 255, 255, 0.407843);
    border-radius: 10px;
    header{
      height: 39px;
      padding: 10px;
      border-bottom: 1px solid #666;
      h1{
        text-align: center;
        font-size: 25px;
        line-height: 40px;
      }
    }
    footer{
      height: 25px;
      padding: 0px 10px 0 10px;
      p{
        line-height: 25px;
      }
    }
  }
}
#login{
  height: 100%;
  display: flex;
  background: url('../assets/img/login-bg.jpg') no-repeat;
  background-size: cover;
  background-position: center bottom;
  justify-content: center;
  align-items: center;
  .login-form{
    padding: 10px;
    overflow: hidden;
    .icon{
      color: #666;
      font-size: 18px;
      position: absolute;
      z-index: 1;
      left: 8px;
    }
  }
}
</style>

<style lang="less">
#demo-canvas{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.login-form{
  .el-form{
    width: 270px;
    margin: 0 auto;
  }
  .el-form-item__content{
    margin-left: 0!important;
    display: flex;
    .el-button{
      width: 100%;
    }
    .el-input{
      flex: 1;
      &.code{
        .el-input__inner{
          padding-left: 15px;
        }
      }
      .el-input__inner{
        padding-left: 30px;
      }
    }
    .codeimg{
      width: 100px;
      height: 39px;
      margin-left: 5px;
    }
  }
}
</style>



