<!-- 牛牛开奖 -->
<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item three">
        <span class="title">房间名称：</span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="nnAwardId" label="ID" width="100"></el-table-column>
        <el-table-column prop="roomName" label="房间名称" width="100"></el-table-column>
        <el-table-column prop="beginBetDate" label="开盘时间"></el-table-column>
        <el-table-column prop="endBetDate" label="封盘时间" width="100"></el-table-column>
        <el-table-column prop="sendDate" label="发包时间"></el-table-column>
        <el-table-column prop="awardDate" label="开奖时间" width="100"></el-table-column>
        <el-table-column prop="resultDate" label="结算时间" width="100"></el-table-column>
        <el-table-column prop="awardStatus" label="开奖状态">
          <template slot-scope="scope">
            <span v-if="scope.row.awardStatus===1" style="color: #666">下注中</span>
            <span v-if="scope.row.awardStatus===5" style="color: #eb453c">发包中</span>
            <span v-if="scope.row.awardStatus===2" style="color: green">领包开奖</span>
            <span v-if="scope.row.awardStatus===3" style="color: #eb453c">下注结算</span>
            <span v-if="scope.row.awardStatus===4" style="color: #eb453c">已结算</span>
            <span v-if="scope.row.awardStatus===6" style="color: #666">已返还</span>
          </template>
        </el-table-column>
        <el-table-column prop="totalBetPrice" label="下注总金额" width="100"></el-table-column>
        <el-table-column prop="sendPrice" label="发包金额" width="100"></el-table-column>
        <el-table-column prop="openPrice" label="拆包金额"></el-table-column>
        <el-table-column prop="betPeopleNum" label="下注人数"></el-table-column>
        <el-table-column prop="betRobotNum" label="下注机器人数"></el-table-column>
        <el-table-column prop="betWaterRatio" label="押注抽水比例"></el-table-column>
        <el-table-column prop="betWaterPrice" label="押注抽水金额"></el-table-column>
        <el-table-column prop="bonusPoolNum" label="奖金池数量"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>ID</label>{{dialogData.nnAwardId}}</p>
        <p><label>房间名称</label>{{dialogData.roomName}}</p>
        <p><label>开盘时间</label>{{dialogData.beginBetDate}}</p>
        <p><label>封盘时间</label>{{dialogData.endBetDate}}</p>
        <p><label>发包时间</label>{{dialogData.sendDate}}</p>
        <p><label>开奖时间</label>{{dialogData.awardDate}}</p>
        <p><label>结算时间</label>{{dialogData.resultDate}}</p>
        <p><label>开奖状态</label>{{dialogData.awardStatus}}</p>
        <p><label>下注总金额</label>{{dialogData.totalBetPrice}}</p>
        <p><label>发包金额</label>{{dialogData.sendPrice}}</p>
        <p><label>拆包金额</label>{{dialogData.openPrice}}</p>
        <p><label>下注人数</label>{{dialogData.betPeopleNum}}</p>
        <p><label>下注机器人数</label>{{dialogData.betRobotNum}}</p>
        <p><label>押注抽水比例</label>{{dialogData.betWaterRatio}}</p>
        <p><label>押注抽水金额</label>{{dialogData.betWaterPrice}}</p>
        <p><label>奖金池数量</label>{{dialogData.bonusPoolNum}}</p>
        <p><label>创建时间</label>{{dialogData.createDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameNnAwardNnAwardList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, roomName: ''},
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameNnAwardNnAwardList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._gameNnAwardNnAwardList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameNnAwardNnAwardList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._gameNnAwardNnAwardList()
    },
    async _gameNnAwardNnAwardList () {
      const res = await gameNnAwardNnAwardList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameNnAwardNnAwardList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



