<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">领包玩家：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">发包玩家：</span>
        <el-input v-model="requestData.sendName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">中雷数量：</span>
        <el-input v-model="requestData.lossNum" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">发包类型：</span>
        <el-select clearable v-model="requestData.sendType" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label="领包玩家"></el-table-column>
        <el-table-column prop="sendName" label="发包人名称"></el-table-column>
        <el-table-column prop="sendType" label="发包类型">
          <template slot-scope="scope">
            <span v-if="scope.row.sendType===1">玩家发包</span>
            <span v-if="scope.row.sendType===2">机器人发包</span>
          </template>
        </el-table-column>
        <el-table-column prop="robotType" label="机器人状态">
          <template slot-scope="scope">
            <span v-if="scope.row.robotType===1" style="color:green">开启</span>
            <span v-if="scope.row.robotType===2" style="color:#eb453c">禁用</span>
            <span v-if="scope.row.robotType===3" style="color:#666">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="lossNum" label="中雷数量"></el-table-column>
        <el-table-column prop="lossReward" label="中雷奖励">
        </el-table-column>
        <el-table-column prop="validNum" label="玩家当前余额"></el-table-column>
        <el-table-column prop="rewardDate" label="奖励时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>领包玩家</label>{{dialogData.userName}}</p>
        <p><label>发包人名称</label>{{dialogData.sendName}}</p>
        <p><label>发包类型</label><span v-if="dialogData.sendType===1">玩家发包</span><span v-if="dialogData.sendType===2">机器人发包</span></p>
        <p><label>机器人状态</label><span v-if="dialogData.robotType===1" style="color:green">开启</span><span v-if="dialogData.robotType===2" style="color:#eb453c">禁用</span><span v-if="dialogData.robotType===3" style="color:#666">删除</span></p>
        <p><label>中雷数量</label>{{dialogData.lossNum}}</p>
        <p><label>中雷奖励</label>{{dialogData.lossReward}}</p>
        <p><label>玩家当前余额</label>{{dialogData.validNum}}</p>
        <p><label>奖励时间</label>{{dialogData.rewardDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlLossInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', sendName: '', lossNum: '', sendType: ''},
      options: [{value: 1, label: '玩家发包'}, {value: 2, label: '机器人发包'}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameSlLossInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._gameSlLossInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlLossInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._gameSlLossInfoList()
    },
    async _gameSlLossInfoList () {
      const res = await gameSlLossInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.lossInfoList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameSlLossInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



