<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">禁用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">启用</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="Id" width="60"></el-table-column>
        <el-table-column prop="configureType" label="类型" width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.configureType === 1">支付宝</span>
            <span v-if="scope.row.configureType === 2">银联</span>
          </template>
        </el-table-column>
        <el-table-column prop="minPrice" label="最低充值金额" min-width="100"></el-table-column>
        <el-table-column prop="maxPrice" label="最高充值金额" min-width="100"></el-table-column>
        <el-table-column prop="gaveRatio" label="充值赠送比例" min-width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="60">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建者" width="100"></el-table-column>
        <el-table-column prop="createDate" label="创建时间" width="140"></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="edit(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="编辑充值配置" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" :rules="rules" label-width="100px" size="mini">
        <el-form-item label="最低充值金额" prop="minPrice">
          <el-input style="width:180px" v-model="form.minPrice" placeholder="请输入最低充值金额"></el-input>
        </el-form-item>
        <el-form-item label="最高充值金额" prop="maxPrice">
          <el-input style="width:180px" v-model="form.maxPrice" placeholder="请输入最高充值金额"></el-input>
        </el-form-item>
        <el-form-item label="充值赠送比例" prop="gaveRatio">
          <el-input style="width:180px" v-model="form.gaveRatio" placeholder="请输入充值赠送比例"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {RechargeConfigureList, RechargeConfigureEdit, RechargeConfigureUpdateStatus} from '@/api'
import {baseUrl, checkSmallNumber, checkPic} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      dialogFormVisible: false,
      form: {id: '', minPrice: '', maxPrice: '', gaveRatio: ''},
      choseArr: [],
      totalNum: 0,
      serachData: {pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      checkedId: '',
      checked: [],
      advertIds: [],
      rules: {
        gaveRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ],
        minPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        maxPrice: [
          { validator: checkPic, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._RechargeConfigureList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._RechargeConfigureList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _RechargeConfigureList () {
      const res = await RechargeConfigureList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.rechargeConfigureList
      }
    },
    async submit () {
      if (this.form.minPrice === '') {
        this.$message({type: 'warning', message: '请输入最低充值金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.minPrice)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.form.maxPrice === '') {
        this.$message({type: 'warning', message: '请输入最高充值金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.maxPrice)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.form.gaveRatio === '') {
        this.$message({type: 'warning', message: '请输入充值赠送比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.form.gaveRatio)) {
        this.$message({type: 'warning', message: '请输入非负小数'})
      } else {
        const res = await RechargeConfigureEdit(this.form)
        if (res.result === '1') {
          this._RechargeConfigureList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    async edit (row) {
      this.dialogFormVisible = true
      this.form.id = row.id
      this.form.minPrice = row.minPrice
      this.form.maxPrice = row.maxPrice
      this.form.gaveRatio = row.gaveRatio
    },
    async _freeze (row) {
      const res = await RechargeConfigureUpdateStatus({status: row, id: this.choseArr[0].id})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.advertIds = []
        this._RechargeConfigureList()
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一条充值配置进行操作!'})
      } else {
        let tip
        row === 1 ? tip = '此操作将启用该充值配置，是否继续？' : tip = '此操作将禁用该充值配置，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {id: '', minPrice: '', maxPrice: '', gaveRatio: ''}
    },
    refresh () {
      this.serachData = {pageNum: 1, pageSize: 20}
      this._RechargeConfigureList()
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._RechargeConfigureList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
}
.editor-container {
  max-height: 500px;
  margin: 0 0 30px;
  .editor-upload-btn-container {
    text-align: right;
    margin-right: 10px;
    .editor-upload-btn {
      display: inline-block;
    }
  }
}
</style>



