<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item five">
        <span class="title">上级地区ID：</span>
        <el-input v-model="serachData.parentId" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">地区级别：</span>
        <el-input v-model="serachData.areaName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">地区级别：</span>
        <el-select clearable v-model="serachData.areaLevel" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="countryAreaId" label="国家地区ID"></el-table-column>
        <el-table-column prop="countryName" label="国家名称"></el-table-column>
        <el-table-column prop="stateName" label="省/州"></el-table-column>
        <el-table-column prop="cityName" label="城市名称"></el-table-column>
        <el-table-column prop="regionName" label="地区名称"></el-table-column>
        <el-table-column prop="parentId" label="上级ID"></el-table-column>
        <el-table-column prop="areaLevel" label="地区等级">
          <template slot-scope="scope">
            <span v-if="scope.row.areaLevel === 1">国家</span>
            <span v-if="scope.row.areaLevel === 2">州/省</span>
            <span v-if="scope.row.areaLevel === 3">城市</span>
            <span v-if="scope.row.areaLevel === 4">地区</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import {areaList} from '@/api'
export default {
  data () {
    return {
      totalNum: 0,
      serachData: {parentId: '', areaName: '', areaLevel: '', pageNum: 1, pageSize: 20},
      options: [{value: 1, label: '国家'}, {value: 2, label: '州/省'}, {value: 3, label: '城市'}, {value: 4, label: '地区'}],
      tableData: []
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._areaList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._areaList()
    },
    async _areaList () {
      const res = await areaList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.countryAreaList
      }
    },
    refresh () {
      this.serachData = {parentId: '', areaName: '', areaLevel: '', pageNum: 1, pageSize: 20}
      this._areaList()
    },
    search () {
      this.serachData.pageNum = 1
      this._areaList()
    }
  },
  created () {
    this._areaList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
</style>



