<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">软件名称：</span>
        <el-input v-model="serachData.appName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">升级类型：</span>
        <el-select clearable v-model="serachData.updateType" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="search-item four">
        <span class="title">更新内容：</span>
        <el-input v-model="serachData.updateContent" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addVersion">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit">编辑</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(1)">启用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(2)">禁用</el-button>
        <!-- <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button> -->
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="versionList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="versionId" label="版本ID" width="60"></el-table-column>
        <el-table-column prop="appName" label="软件名称" width="100"></el-table-column>
        <el-table-column prop="updateType" label="升级类型" width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.updateType === 1" style="color:#eb453c">强制升级</span>
            <span v-if="scope.row.updateType === 2" style="color:#67c23a">选择升级</span>
            <span v-if="scope.row.updateType === 3" style="color:#999">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="versionName" label="版本名称" width="120"></el-table-column>
        <el-table-column prop="apkCode" label="APK号" width="100"></el-table-column>
        <el-table-column prop="apkPackage" label="APK包名" width="120"></el-table-column>
        <el-table-column prop="apkSize" label="APK大小" width="100"></el-table-column>
        <el-table-column prop="updateContent" label="更新内容" mim-width="150"></el-table-column>
        <el-table-column prop="updateDate" label="更新时间" width="140"></el-table-column>
        <el-table-column prop="versionUrl" label="版本地址" width="150"></el-table-column>
        <el-table-column prop="createSysUserName" label="创建人" width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="60">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">禁用</span>
            <span v-if="scope.row.status === 3" style="color:#999">删除</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="isedit ? '编辑版本信息': '新增版本信息'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="90px" size="mini">
        <el-form-item label="软件名称">
          <el-input style="width:192px" v-model="form.appName" placeholder="请输入软件名称" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="升级类型">
          <el-select clearable v-model="form.updateType" placeholder="请选择">
            <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="更新内容">
          <el-input type="textarea" :rows="3" style="width:192px" v-model="form.updateContent" placeholder="请输入更新内容" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="更新时间">
          <el-date-picker
          size="mini"
          style="width: 187px;"
          v-model="form.updateDate"
          type="datetime"
          value-format="yyyy-MM-dd HH:mm:ss"
          placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="上传安装包">
          <input type="file" ref="uploadInput" @change="_uploadChange" accept=".apk">
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {uploadApk, versionList, versionAdd, versionUpdate, versionUpdateStatus} from '@/api'
export default {
  data () {
    return {
      dialogFormVisible: false,
      isedit: false,
      form: {appName: '', updateType: '', updateContent: '', updateDate: '', versionUrl: ''},
      choseArr: [],
      serachData: {appName: '', updateType: '', updateContent: ''},
      versionList: [],
      roleList: [],
      userid: '',
      options: [{value: 1, label: '强制升级'}, {value: 2, label: '选择升级'}]
    }
  },
  methods: {
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _uploadChange (e) {
      this.key = new FormData()
      this.key.append('file', e.target.files[0])
      const res = await uploadApk(this.key)
      if (res.result === '1') {
        this.form.versionUrl = res.datas.fileUrl
      }
    },
    edit () {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一个版本进行编辑'})
      } else {
        this.dialogFormVisible = true
        this.form.versionId = this.choseArr[0].versionId
        this.form.appName = this.choseArr[0].appName
        this.form.updateType = this.choseArr[0].updateType
        this.form.updateContent = this.choseArr[0].updateContent
        this.form.updateDate = this.choseArr[0].updateDate
        this.form.versionUrl = this.choseArr[0].versionUrl
        this.isedit = true
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一个版本进行操作'})
      } else {
        let tip
        row === 1 ? tip = '此操作将启用版本，是否继续？' : row === 2 ? tip = '此操作将禁用版本，是否继续？' : tip = '此操作将删除版本，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    async _freeze (row) {
      const res = await versionUpdateStatus({status: row, versionId: this.choseArr[0].versionId})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.getVersionList()
      }
    },
    async getVersionList () {
      const res = await versionList(this.serachData)
      if (res.result === '0') {
        this.versionList = res.datas.versionList
      }
    },
    async submit () {
      if (this.form.appName === '') {
        this.$message({type: 'warning', message: '请填写软件名称'})
      } else if (this.form.updateType === '') {
        this.$message({type: 'warning', message: '请选择升级类型'})
      } else if (this.form.updateContent === '') {
        this.$message({type: 'warning', message: '请填写更新内容'})
      } else if (this.form.updateDate === '') {
        this.$message({type: 'warning', message: '请选择更新时间'})
      } else if (this.form.versionUrl === '') {
        this.$message({type: 'warning', message: '请上传安装包'})
      } else if (!this.isedit) {
        const res = await versionAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this.serachData.pageNum = 1
          this.getVersionList()
          this.setReset()
        }
      } else {
        const res = await versionUpdate(this.form)
        if (res.result === '1') {
          this.getVersionList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addVersion () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {appName: '', updateType: '', updateContent: '', updateDate: '', versionUrl: ''}
    },
    search () {
      this.serachData.pageNum = 1
      this.getVersionList()
    },
    refresh () {
      this.serachData = {appName: '', updateType: '', updateContent: ''}
      this.getVersionList()
    }
  },
  created () {
    this.getVersionList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 40px);
  }
  .el-dialog__body .my-table{
    height: 350px;
  }
}
</style>



