<template>
  <div class="con">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="mini">
      <el-form-item label="发包间隔" prop="sendInterval">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.sendInterval"></el-input> 秒（发包间隔时长内没玩家发包，则机器人发包）
      </el-form-item>
      <el-form-item label="领包间隔" prop="receiveInterval">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.receiveInterval"></el-input> 秒（领包间隔时长内没玩家领包，机器人开始领包（一个红包每个机器人只领一次））
      </el-form-item>
      <el-form-item label="返还间隔" prop="returnInterval">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.returnInterval"></el-input> 秒（返还间隔时长内红包没领完，红包余额退还发包玩家/机器人的账号上）
      </el-form-item>
      <el-form-item label="发包出雷比例" prop="sendLossRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.sendLossRatio"></el-input>
      </el-form-item>
      <el-form-item label="领包中雷比例" prop="receiveLossRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.receiveLossRatio"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {gameSlRobotConfigSlRobotConfigInfo, gameSlRobotConfigUpdateSlRobotConfig} from '@/api'
import {checkInt, checkTwoFlaseSmallNumber} from '@/utils/commom'
export default {
  data () {
    return {
      ruleForm: {
        slRobotConfigId: '',
        sendInterval: '',
        receiveInterval: '',
        sendLossRatio: '',
        returnInterval: '',
        receiveLossRatio: ''
      },
      rules: {
        sendInterval: [
          { validator: checkInt, trigger: 'change' }
        ],
        receiveInterval: [
          { validator: checkInt, trigger: 'change' }
        ],
        sendLossRatio: [
          { validator: checkTwoFlaseSmallNumber, trigger: 'change' }
        ],
        returnInterval: [
          { validator: checkInt, trigger: 'change' }
        ],
        receiveLossRatio: [
          { validator: checkTwoFlaseSmallNumber, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    async _gameSlRobotConfigSlRobotConfigInfo () {
      const res = await gameSlRobotConfigSlRobotConfigInfo({})
      if (res.result === '0') {
        this.ruleForm.slRobotConfigId = res.datas.id
        this.ruleForm.sendInterval = res.datas.sendInterval
        this.ruleForm.receiveInterval = res.datas.receiveInterval
        this.ruleForm.sendLossRatio = res.datas.sendLossRatio
        this.ruleForm.returnInterval = res.datas.returnInterval
        this.ruleForm.receiveLossRatio = res.datas.receiveLossRatio
      }
    },
    async submitForm () {
      if (this.ruleForm.sendInterval === '') {
        this.$message({type: 'warning', message: '请输入发包间隔'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.ruleForm.sendInterval)) {
        this.$message({type: 'warning', message: '发包间隔请输入正整数'})
      } else if (this.ruleForm.receiveInterval === '') {
        this.$message({type: 'warning', message: '请输入领包间隔'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.ruleForm.receiveInterval)) {
        this.$message({type: 'warning', message: '领包间隔请输入正整数'})
      } else if (this.ruleForm.returnInterval === '') {
        this.$message({type: 'warning', message: '请输入返还间隔'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.ruleForm.returnInterval)) {
        this.$message({type: 'warning', message: '返还间隔请输入正整数'})
      } else if (this.ruleForm.sendLossRatio === '') {
        this.$message({type: 'warning', message: '请输入发包出雷比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{2}))$/.test(this.ruleForm.sendLossRatio)) {
        this.$message({type: 'warning', message: '发包出雷比例请输入2位非负小数'})
      } else if (this.ruleForm.receiveLossRatio === '') {
        this.$message({type: 'warning', message: '请输入领包中雷比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{2}))$/.test(this.ruleForm.receiveLossRatio)) {
        this.$message({type: 'warning', message: '领包中雷比例请输入2位非负小数'})
      } else {
        await gameSlRobotConfigUpdateSlRobotConfig(this.ruleForm)
      }
    }
  },
  created () {
    this._gameSlRobotConfigSlRobotConfigInfo()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
}
</style>



