<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="id" label="Id" width="80"></el-table-column>
        <el-table-column prop="lossNum" label="中雷数量" width="100"></el-table-column>
        <el-table-column prop="lossReward" label="中雷奖励" width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">正常</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="modifySysUserName" label="修改人" width="90"></el-table-column>
        <el-table-column prop="modifyDate" label="修改时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="220">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">修改中雷配置</el-button>
            <el-button v-if="scope.row.status===1" type="primary" @click.native.prevent="freeze(scope.row)" size="mini">禁用</el-button>
            <el-button v-else type="primary" @click.native.prevent="freeze(scope.row)" size="mini">解禁</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="修改中雷配置" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" :rules="rules" label-width="80px" size="mini">
        <el-form-item label="中雷数量" prop="lossNum">
          <el-input style="width:180px" v-model="form.lossNum" placeholder="请输入中雷数量" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="中雷奖励" prop="lossReward">
          <el-input style="width:180px" v-model="form.lossReward" placeholder="请输入中雷奖励" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameLossConfigList, gameLossConfigUpdateStatus, gameLossConfigEdit} from '@/api'
import {checkInt, checkPic} from '@/utils/commom'
export default {
  data () {
    return {
      ConfigList: [],
      dialogFormVisible: false,
      form: {lossNum: '', lossReward: '', id: ''},
      rules: {
        lossReward: [
          { validator: checkPic, trigger: 'change' }
        ],
        lossNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    refresh () {
      this._gameLossConfigList()
    },
    async _gameLossConfigList () {
      const res = await gameLossConfigList({})
      if (res.result === '0') {
        this.ConfigList = res.datas.lossConfigList
      }
    },
    freeze (row) {
      let tip
      row.status === 1 ? tip = '此操作将禁用该扫雷中雷配置，是否继续？' : tip = '此操作将解禁该扫雷中雷配置，是否继续？'
      this.$confirm(tip, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._freeze(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '操作已取消'
        })
      })
    },
    async _freeze (row) {
      let status = row.status === 1 ? 2 : 1
      const res = await gameLossConfigUpdateStatus({status: status, id: row.id})
      if (res.result === '1') {
        this._gameLossConfigList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.form.id = row.id
      this.form.lossNum = row.lossNum
      this.form.lossReward = row.lossReward
    },
    async submit () {
      if (this.form.lossNum === '') {
        this.$message({type: 'warning', message: '请输入中雷数量'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.form.lossNum)) {
        this.$message({type: 'warning', message: '请输入正整数'})
      } else if (this.form.lossReward === '') {
        this.$message({type: 'warning', message: '请输入中雷奖励'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.lossReward)) {
        this.$message({type: 'warning', message: '请输入正确的奖励金额'})
      } else {
        const res = await gameLossConfigEdit(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this._gameLossConfigList()
        }
      }
    }
  },
  created () {
    this._gameLossConfigList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 40px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



