<template>
  <aside class="aside" :class="{active: isCollapse}">
    <div>
      <div class="logo">
        <div v-if="!isCollapse">
          <span>GTJ-ADMIN</span>
        </div>
        <img src="http://www.layui.com/admin/pro/dist/style/res/logo.png" v-else alt="">
      </div>
      <el-menu :default-active="$route.path" :unique-opened="true" class="menu" :collapse="isCollapse" router background-color="#333" text-color="#fff" active-text-color="#ffd04b">
        <el-submenu :index="item.path" v-if="!item.hidden" v-for="(item, index) in routerMenu" :key="index">
          <template slot="title">
            <i class="icon iconfont" :class="item.meta.icon"></i>
            <span slot="title">{{item.meta.title}}</span>
          </template>
          <div>
            <el-menu-item-group v-for="(child, i) in item.children" v-if="!child.hidden" :key="i">
              <el-menu-item :index="child.path">{{child.meta.titel}}</el-menu-item>
            </el-menu-item-group>
          </div>
        </el-submenu>
      </el-menu>
    </div>
  </aside>
</template>

<script>
import {menu} from '@/router'
import {sysInfo} from '@/api'
export default {
  props: {
    isCollapse: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      routerMenu: []
    }
  },
  methods: {
    async _sysInfo () {
      const res = await sysInfo()
      if (res.result === '0') {
        res.datas.oneMenuList.map((value1) => {
          value1.twoMenuList.map((value2) => {
            menu.map((item) => {
              if (item.path === value1.menuUrl) {
                item.hidden = false
                item.meta.title = value1.menuName
                item.children.map((value) => {
                  if (value2.menuUrl === value.path) {
                    value.meta.titel = value2.menuName
                    value.hidden = false
                  }
                })
              }
            })
          })
        })
        this.$store.commit('ROUTER_MENU', menu)
        this.routerMenu = menu
      }
    }
  },
  created () {
    this.routerMenu = this.$store.getters.routerMenu
    if (this.$store.getters.routerMenu.length === 0) {
      this._sysInfo()
    }
  }
}
</script>

<style lang="less" scoped>
.aside{
  width: 165px;
  overflow-x: hidden;
  height: 100%;
  overflow-y: auto;
  background-color: #333;
  transition: all 0.3s;
  &.active{
    width: 50px;
  }
  &::-webkit-scrollbar{
    width: 0px;
    height: 0px;
    background-color: transparent;
  }
  .logo{
    text-align: center;
    color: #fff;
    line-height: 50px;
  }
  .icon{
    font-size: 18px;
    color: #fff;
    margin-right: 5px;
  }
}
</style>



