<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">商品名称：</span>
        <el-input v-model="requestData.goodsName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="requestData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addGood">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit">编辑</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">冻结</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">恢复正常</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="goodsDeployList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="goodsName" label="商品名称" width="100"></el-table-column>
        <el-table-column prop="goodsLogo" label="商品LOGO" width="120">
          <template slot-scope="scope">
            <img style="width: 90px" :src="baseUrl+scope.row.goodsLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="goodsNum" label="商品库存" width="100"></el-table-column>
        <el-table-column prop="goodsPayNum" label="商品销售量" width="100"></el-table-column>
        <el-table-column prop="priceOldNum" label="原价金额" width="100"></el-table-column>
        <el-table-column prop="priceNum" label="现价金额" width="100"></el-table-column>
        <el-table-column prop="sort" label="排序号" width="100"></el-table-column>
        <el-table-column prop="deployLogo" label="配置LOGO" width="120">
          <template slot-scope="scope">
            <img style="width: 90px" :src="baseUrl+scope.row.deployLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态" width="80">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">正常</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">冻结</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建人" width="100"></el-table-column>
        <el-table-column prop="createDate" label="创建时间" ></el-table-column>
        <el-table-column prop="modifySysUserName" label="修改人" width="100"></el-table-column>
        <el-table-column prop="modifyDate" label="修改时间" ></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="商品详情信息" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>商品名称</label>{{dialogData.goodsName}}</p>
        <p><label>商品LOGO</label><img :src="baseUrl + dialogData.goodsLogo" @click="showImg(baseUrl + dialogData.goodsLogo)" alt=""></p>
        <p><label>商品库存</label>{{dialogData.goodsNum}}</p>
        <p><label>商品销售量</label>{{dialogData.goodsPayNum}}</p>
        <p><label>原价金额</label>{{dialogData.priceOldNum}}</p>
        <p><label>现价金额</label>{{dialogData.priceNum}}</p>
        <p><label>配置LOGO</label><img :src="baseUrl + dialogData.deployLogo" @click="showImg(baseUrl + dialogData.deployLogo)" alt=""></p>
        <p><label>排序号</label>{{dialogData.sort}}</p>
        <p>
          <label>状态</label>
          <span v-if="dialogData.status===1" style="color: green">正常</span>
          <span v-if="dialogData.status===2" style="color: #eb453c">禁用</span>
        </p>
        <p><label>创建时间</label>{{dialogData.createDate}}</p>
      </div>
    </el-dialog>
     <el-dialog :title="isedit ? '编辑商品信息': '添加商品'" :visible.sync="dialogFormVisible2" width="450px">
      <el-form :model="form" :rules="rules" label-width="120px" size="mini">
        <el-form-item label="商城商品LOGO :">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="商城商品 :">
          <el-select clearable v-model="form.goodsId" placeholder="请选择" style="width:260px">
            <el-option v-for="(item, index) in options2" :key="index" :label="item.goodsName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="原价金额 :" prop="priceOldNum">
          <el-input style="width:260px" v-model="form.priceOldNum"  placeholder="请输入原价金额" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="现价金额 :" prop="priceNum">
          <el-input style="width:260px" v-model="form.priceNum"  placeholder="请输入现价金额"  autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="序号 :">
          <el-input style="width:260px" v-model="form.sort"  placeholder="请输入序号" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {goodsDeployList, goodsList, goodsDeployEditStatus, goodsDeployEdit, goodsDeployAdd} from '@/api'
import MyUpload from '@/components/Upload'
import {baseUrl, checkPic} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, goodsName: '', status: ''},
      goodsDeployList: [],
      choseArr: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogData: {},
      goodsDeployIds: [],
      isedit: false,
      goodsList: [],
      count: 0,
      option: {
        type: 1,
        file: [],
        count: 1
      },
      options: [{value: 1, label: '正常'}, {value: 2, label: '禁用'}],
      options2: [],
      baseUrl: '',
      form: {goodsId: '', deployLogo: '', priceOldNum: '', priceNum: '', sort: ''},
      rules: {
        priceOldNum: [
          { validator: checkPic, trigger: 'change' }
        ],
        priceNum: [
          { validator: checkPic, trigger: 'change' }
        ]
      }
    }
  },
  components: {
    MyUpload
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._goodsList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._goodsList()
    },
    async getGoodsList () {
      const res = await goodsList({})
      if (res.result === '0') {
        this.options2 = res.datas.goodsList
      }
    },
    async edit (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一款商品进行编辑'})
      } else {
        this.option.file = []
        this.dialogFormVisible2 = true
        this.form.goodsDeployId = this.choseArr[0].id
        this.form.goodsId = this.choseArr[0].goodsId
        this.form.deployLogo = this.choseArr[0].goodsLogo
        this.form.priceOldNum = this.choseArr[0].priceOldNum
        this.form.priceNum = this.choseArr[0].priceNum
        this.form.sort = this.choseArr[0].sort
        this.option.file.push({file: this.choseArr[0].deployLogo})
        this.isedit = true
      }
    },
    async submit () {
      if (this.form.deployLogo === '') {
        this.$message({type: 'warning', message: '请上传商城商品LOGO'})
      } else if (this.form.goodsId === '') {
        this.$message({type: 'warning', message: '请选择商城商品'})
      } else if (this.form.priceOldNum === '') {
        this.$message({type: 'warning', message: '请填写商品原价金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.priceOldNum)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.form.priceNum === '') {
        this.$message({type: 'warning', message: '请填写商品现价金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.priceNum)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.form.sort === '') {
        this.$message({type: 'warning', message: '请输入序号'})
      } else if (!this.isedit) {
        const res = await goodsDeployAdd(this.form)
        if (res.result === '1') {
          this._goodsList()
          this.dialogFormVisible2 = false
          this.setReset()
        }
      } else {
        const res = await goodsDeployEdit(this.form)
        if (res.result === '1') {
          this._goodsList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible2 = false
          this.setReset()
        }
      }
    },
    upfiles (file) {
      this.form.deployLogo = file.files[0].file
    },
    updelete (file) {
      this.form.deployLogo = ''
    },
    addGood () {
      this.dialogFormVisible2 = true
      this.isedit = false
      this.setReset()
    },
    cancle () {
      this.dialogFormVisible2 = false
      this.setReset()
    },
    setReset () {
      this.option.file = []
      this.form = {goodsId: '', deployLogo: '', priceOldNum: '', priceNum: '', sort: ''}
    },
    search () {
      this.requestData.pageNum = 1
      this._goodsList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, goodsName: '', status: ''}
      this._goodsList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _goodsList () {
      const res = await goodsDeployList(this.requestData)
      if (res.result === '0') {
        this.goodsDeployList = res.datas.goodsDeployList
        this.totalNum = res.datas.totalRow
      }
    },
    async _freeze (row) {
      const res = await goodsDeployEditStatus({type: row, goodsDeployIds: this.goodsDeployIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this._goodsList()
      }
    },
    freeze (row) {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一个商品'})
      } else {
        this.goodsDeployIds = this.choseArr.map((item) => {
          return item.id
        })
        let tip
        row === 1 ? tip = '此操作将批量解冻选择的商品，是否继续？' : row === 2 ? tip = '此操作将批量冻结选择的商品，是否继续？' : tip = '此操作将批量删除选择的商品，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._goodsList()
    this.getGoodsList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



