<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">用户名称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">房间名称：</span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">手气最佳：</span>
        <el-select clearable v-model="requestData.luckStatus" placeholder="请选择">
          <el-option v-for="(item, index) in options1" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="search-item four">
        <span class="title">领包类型：</span>
        <el-select clearable v-model="requestData.receiveType" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label="玩家名称"></el-table-column>
        <el-table-column prop="roomId" label="房间ID" width="80"></el-table-column>
        <el-table-column prop="bankName" label="房间信息" min-width="180">
          <template slot-scope="scope">
            <div style="float:left;">
              <img style="display: inline-block;width: 30px;vertical-align: middle;" :src="baseUrl + scope.row.roomLogo" alt="">
              {{scope.row.roomName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="sendUserName" label="发包人名称"></el-table-column>
        <el-table-column prop="receiveType" label="领包类型">
          <template slot-scope="scope">
            <span v-if="scope.row.receiveType===1">玩家领包</span>
            <span v-if="scope.row.receiveType===2">系统领包</span>
          </template>
        </el-table-column>
        <el-table-column prop="receivePrice" label="领包金额"></el-table-column>
        <el-table-column prop="luckStatus" label="手气最佳">
          <template slot-scope="scope">
            <span v-if="scope.row.luckStatus===1">正常</span>
            <span v-if="scope.row.luckStatus===2">最佳</span>
            <span v-if="scope.row.luckStatus===3">最低</span>
          </template>
        </el-table-column>
        <el-table-column prop="receiveDate" label="领包时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>玩家名称</label>{{dialogData.userName}}</p>
        <p><label>房间名称</label>{{dialogData.roomName}}</p>
        <p><label>房间logo</label><img :src="baseUrl + dialogData.roomLogo" @click="showImg(baseUrl + dialogData.roomLogo)" alt=""></p>
        <p><label>发包人名称</label>{{dialogData.sendUserName}}</p>
        <p><label>领包类型</label><span v-if="dialogData.receiveType===1">玩家领包</span><span v-if="dialogData.receiveType===2">系统领包</span></p>
        <p><label>领包金额</label>{{dialogData.receivePrice}}</p>
        <p><label>手气最佳</label><span v-if="dialogData.luckStatus===1">正常</span><span v-if="dialogData.luckStatus===2">最佳</span><span v-if="dialogData.luckStatus===3">最低</span></p>
        <p><label>领包时间</label>{{dialogData.receiveDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameJlReceiveInfoList} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      requestData: {pageNum: 1, pageSize: 20, userName: '', luckStatus: '', receiveType: '', roomName: ''},
      options: [{value: 1, label: '玩家领包'}, {value: 2, label: '系统领包'}],
      options1: [{value: 1, label: '正常'}, {value: 2, label: '最佳'}, {value: 3, label: '最低'}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameJlReceiveInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._gameJlReceiveInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameJlReceiveInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', luckStatus: '', receiveType: '', roomName: ''}
      this._gameJlReceiveInfoList()
    },
    async _gameJlReceiveInfoList () {
      const res = await gameJlReceiveInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.receiveInfoList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._gameJlReceiveInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



