<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addRole">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">禁用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">启用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="Id" width="60"></el-table-column>
        <el-table-column prop="noticeTitle" label="公告标题" width="200"></el-table-column>
        <el-table-column prop="noticeContent" label="公告内容"></el-table-column>
        <el-table-column prop="status" label="状态" width="60">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建者" width="100"></el-table-column>
        <el-table-column prop="createDate" label="创建时间" width="140"></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="edit(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑游戏公告': '新增游戏公告'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="100px" size="mini">
        <el-form-item label="公告标题">
          <el-input style="width:180px" v-model="form.noticeTitle" placeholder="请输入公告标题"></el-input>
        </el-form-item>
        <el-form-item label="公告内容">
          <el-input type="textarea" :rows="5" style="width:180px" v-model="form.noticeContent" placeholder="请输入公告内容"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {noticeGameNoticeList, noticeAdd, noticeInfo, noticeEdit, noticeUpdateStatus} from '@/api'
export default {
  data () {
    return {
      dialogFormVisible: false,
      isedit: false,
      form: {noticeTitle: '', noticeContent: ''},
      choseArr: [],
      totalNum: 0,
      serachData: {pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      checkedId: '',
      checked: [],
      advertIds: []
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._noticeGameNoticeList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._noticeGameNoticeList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _noticeGameNoticeList () {
      const res = await noticeGameNoticeList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.noticeList
      }
    },
    async submit () {
      if (this.form.noticeTitle === '') {
        this.$message({type: 'warning', message: '请输入公告标题'})
      } else if (this.form.noticeContent === '') {
        this.$message({type: 'warning', message: '请输入公告内容'})
      } else if (!this.isedit) {
        this.form.noticeType = 2
        const res = await noticeAdd(this.form)
        if (res.result === '1') {
          this._noticeGameNoticeList()
          this.dialogFormVisible = false
          this.setReset()
        }
      } else {
        const res = await noticeEdit(this.form)
        if (res.result === '1') {
          this._noticeGameNoticeList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addRole () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    async edit (row) {
      this.dialogFormVisible = true
      const res = await noticeInfo({id: row.id})
      this.form.id = row.id
      this.form.noticeTitle = res.datas.noticeTitle
      this.form.noticeContent = res.datas.noticeContent
      this.isedit = true
    },
    async _freeze (row) {
      const res = await noticeUpdateStatus({status: row, id: this.choseArr[0].id})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.advertIds = []
        this._noticeGameNoticeList()
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一条公告进行操作!'})
      } else {
        let tip
        row === 1 ? tip = '此操作将启用公告，是否继续？' : row === 2 ? tip = '此操作将禁用公告，是否继续？' : tip = '此操作将删除公告，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {noticeTitle: '', noticeContent: ''}
    },
    refresh () {
      this.serachData = {pageNum: 1, pageSize: 20}
      this._noticeGameNoticeList()
    }
  },
  created () {
    this._noticeGameNoticeList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
</style>



