<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">游戏名称：</span>
        <el-input v-model="serachData.gameName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="serachData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <!-- <el-button size="mini" type="primary" icon="el-icon-plus" @click="addRole">新增</el-button> -->
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">禁用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">启用</el-button>
        <!-- <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button> -->
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="Id" width="60" fixed></el-table-column>
        <!-- <el-table-column prop="type" label="游戏类型">
          <template slot-scope="scope">
            <span v-if="scope.row.type === 1">扫雷游戏</span>
            <span v-if="scope.row.type === 2">接龙游戏</span>
            <span v-if="scope.row.type === 3">抢庄牛牛</span>
            <span v-if="scope.row.type === 4">禁抢扫雷</span>
          </template>
        </el-table-column> -->
        <el-table-column prop="gameLogo" label="游戏LOGO" width="150">
          <template slot-scope="scope">
            <img style="width:130px" :src="baseUrl + scope.row.gameLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="gameName" label="游戏名称"></el-table-column>
        <el-table-column prop="gameRule" label="游戏规则" width="150">
          <template slot-scope="scope">
            <img style="width:130px" :src="baseUrl + scope.row.gameRule" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建者"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column label="操作" width="80" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="edit(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑游戏': '新增游戏'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="100px" size="mini">
        <el-form-item label="游戏LOGO">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="游戏规则">
          <my-upload :option="option2" @updelete="updelete2" @upfiles="upfiles2"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {gameHallList, gameHallAdd, gameHallInfo, gameHallEdit, gameHallUpdateStatus} from '@/api'
import MyUpload from '@/components/Upload'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      dialogFormVisible: false,
      isedit: false,
      form: {gameLogo: '', gameRule: ''},
      choseArr: [],
      totalNum: 0,
      serachData: {gameName: '', status: '', pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      checkedId: '',
      checked: [],
      option: {
        type: 1,
        file: [],
        count: 1
      },
      option2: {
        type: 1,
        file: [],
        count: 1
      },
      options: [{value: 1, label: '正常'}, {value: 2, label: '禁用'}],
      advertIds: []
    }
  },
  components: {
    MyUpload
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._gameHallList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._gameHallList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    upfiles (file) {
      this.form.gameLogo = file.files[0].file
    },
    updelete (file) {
      this.form.gameLogo = ''
    },
    upfiles2 (file) {
      this.form.gameRule = file.files[0].file
    },
    updelete2 (file) {
      this.form.gameRule = ''
    },
    async _gameHallList () {
      const res = await gameHallList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.gameHallList
      }
    },
    async submit () {
      if (this.form.gameLogo === '') {
        this.$message({type: 'warning', message: '请上传游戏LOGO'})
      } else if (this.form.gameRule === '') {
        this.$message({type: 'warning', message: '请上传规则图片'})
      } else if (!this.isedit) {
        const res = await gameHallAdd(this.form)
        if (res.result === '1') {
          this._gameHallList()
          this.dialogFormVisible = false
          this.setReset()
        }
      } else {
        const res = await gameHallEdit(this.form)
        if (res.result === '1') {
          this._gameHallList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addRole () {
      this.option.file = []
      this.option2.file = []
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    async edit (row) {
      this.option.file = []
      this.option2.file = []
      this.dialogFormVisible = true
      const res = await gameHallInfo({id: row.id})
      this.form.id = row.id
      this.form.type = row.type
      this.form.gameLogo = res.datas.gameLogo
      this.option.file.push({file: row.gameLogo})
      this.form.gameName = res.datas.gameName
      this.form.gameRule = res.datas.gameRule
      this.option2.file.push({file: row.gameRule})
      this.isedit = true
    },
    async _freeze (row) {
      const res = await gameHallUpdateStatus({status: row, id: this.choseArr[0].id})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.advertIds = []
        this._gameHallList()
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一款游戏进行操作!'})
      } else {
        let tip
        row === 1 ? tip = '此操作将启用游戏，是否继续？' : row === 2 ? tip = '此操作将禁用游戏，是否继续？' : tip = '此操作将删除游戏，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {gameLogo: '', gameRule: ''}
    },
    refresh () {
      this.serachData = {gameName: '', status: '', pageNum: 1, pageSize: 20}
      this._gameHallList()
    },
    search () {
      this.serachData.pageNum = 1
      this._gameHallList()
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._gameHallList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
}
.editor-container {
  max-height: 500px;
  margin: 0 0 30px;
  .editor-upload-btn-container {
    text-align: right;
    margin-right: 10px;
    .editor-upload-btn {
      display: inline-block;
    }
  }
}
</style>



