<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item search-id-input">
        <span class="title">玩家ID ：</span>
        <el-input v-model="requestData.userId" clearable></el-input>
      </div>
      <div class="search-item search-phone-input">
        <span class="title">玩家手机：</span>
        <el-input v-model="requestData.phone" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="AliplayList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userId" label="玩家ID"></el-table-column>
        <el-table-column prop="phone" label="玩家手机"></el-table-column>
        <el-table-column prop="userName" label="玩家昵称"></el-table-column>
        <el-table-column prop="trueName" label="玩家姓名"></el-table-column>
        <el-table-column prop="aliPayName" label="支付宝姓名"></el-table-column>
        <el-table-column prop="aliPayNo" label="支付宝账号"></el-table-column>
        <el-table-column prop="receiptCodePic" header-align="center" align="center" width="150px" label="收款码">
          <template slot-scope="scope">
          <el-popover placement="right" title="" trigger="click">
              <img :src="scope.row.receiptCodePic"/><img slot="reference" :src="scope.row.receiptCodePic" :alt="scope.row.receiptCodePic" style="max-height: 50px;max-width: 130px" />
          </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="createDate" label="绑定时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>玩家昵称</label>{{dialogData.userName}}</p>
        <p><label>玩家姓名</label>{{dialogData.trueName}}</p>
        <p><label>支付宝姓名</label>{{dialogData.aliPayName}}</p>
        <p><label>支付宝账号</label>{{dialogData.aliPayNo}}</p>
        <p><label>收款码</label><img :src="dialogData.receiptCodePic" @click="showImg(dialogData.receiptCodePic)" alt=""></p>
        <p><label>绑定时间</label>{{dialogData.createDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {userAliPayAliPayList} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', userId: '', phone: ''},
      AliplayList: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      baseUrl: ''
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._userAliPayAliPayList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._userAliPayAliPayList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userAliPayAliPayList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._userAliPayAliPayList()
    },
    async _userAliPayAliPayList () {
      const res = await userAliPayAliPayList(this.requestData)
      if (res.result === '0') {
        this.AliplayList = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._userAliPayAliPayList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



