<template>
  <div id="app">
    <router-view/>
    <div class="preview-picture" v-show="showImg.previewShow" @click.stop.prevent="showImg.previewShow=false">
      <img :class="{active: showImg.previewShow}" :src="showImg.src !== '' ? showImg.src : ''" alt="">
      <div class="close">
        <i class="el-icon-close"></i>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'App',
  data () {
    return {
      baseUrl: ''
    }
  },
  computed: {
    ...mapGetters([
      'showImg'
    ])
  }
}
</script>

<style lang="less">
@import 'styles/reset.css';
@import 'styles/font/iconfont.css';
@import 'styles/element-ui.css';
html,body{
  height: 100%;
  width: 100%;
}
.all-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.preview-picture{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999999;
  img{
    max-width: 900px;
    max-height: 90%;
    opacity: 0;
    transform: translate3d(0,50px,0);
    transition: all 0.3s;
    &.active{
      opacity: 1;
      transform: translate3d(0,0,0);
    }
  }
  .close {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 30px;
    color: rgba(194,12,12,.7);
    cursor: pointer;
    &:hover{
      color: rgba(194,12,12,.9);
      background-color: #fff;
    }
  }
}
</style>



