<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">支付账号：</span>
        <el-input v-model="serachData.payAccount" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">支付昵称：</span>
        <el-input v-model="serachData.payName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="serachData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addRole">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">禁用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">启用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" fixed></el-table-column>
        <!-- <el-table-column prop="id" label="Id"></el-table-column> -->
        <!-- <el-table-column prop="configureId" label="配置ID"></el-table-column> -->
        <el-table-column prop="configureType" label="充值类型">
          <template slot-scope="scope">
            <span v-if="scope.row.configureType === 1">支付宝</span>
            <span v-if="scope.row.configureType === 2">银联</span>
          </template>
        </el-table-column>
        <el-table-column prop="payAccount" label="支付账号/银行卡卡号">
          <template slot-scope="scope">
            <span v-if="scope.row.configureType === 1">{{scope.row.payAccount}}</span>
            <span v-if="scope.row.configureType === 2">{{scope.row.bankCardNo}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="payName" label="支付昵称/银行卡持卡人">
          <template slot-scope="scope">
            <span v-if="scope.row.configureType === 1">{{scope.row.payName}}</span>
            <span v-if="scope.row.configureType === 2">{{scope.row.cardHolder}}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="bankName" label="银行卡名称"></el-table-column>
        <el-table-column prop="bankCardNo" label="银行卡卡号"></el-table-column>
        <el-table-column prop="cardHolder" label="银行卡持卡人"></el-table-column>
        <el-table-column prop="openingBank" label="开户行"></el-table-column> -->
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建者"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column label="操作" width="100" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="新增充值线下明细配置" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="100px" size="mini">
        <el-form-item label="充值类型">
          <el-select clearable v-model="form.configureId" placeholder="请选择">
            <el-option v-for="(item, index) in options2" :key="index" :label="item.configureType" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付账号" v-if="form.configureId==1">
          <el-input style="width:193px" v-model="form.payAccount" placeholder="请输入支付账号"></el-input>
        </el-form-item>
        <el-form-item label="支付昵称" v-if="form.configureId==1">
          <el-input style="width:193px" v-model="form.payName" placeholder="请输入支付昵称"></el-input>
        </el-form-item>
        <el-form-item label="银行卡名称" v-if="form.configureId==2">
          <el-input style="width:193px" v-model="form.bankName" placeholder="请输入银行卡名称"></el-input>
        </el-form-item>
        <el-form-item label="银行卡卡号" v-if="form.configureId==2">
          <el-input style="width:193px" v-model="form.bankCardNo" placeholder="请输入银行卡卡号"></el-input>
        </el-form-item>
        <el-form-item label="银行卡持卡人" v-if="form.configureId==2">
          <el-input style="width:193px" v-model="form.cardHolder" placeholder="请输入银行卡持卡人"></el-input>
        </el-form-item>
        <el-form-item label="开户行" v-if="form.configureId==2">
          <el-input style="width:193px" v-model="form.openingBank" placeholder="请输入开户行"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="充值线下明细配置详情" :visible.sync="dialogFormVisible2" center width="400px">
      <el-form class="form" ref="form" label-width="110px" label-position="left" size="mini">
        <!-- <el-form-item label="ID">
          <div>{{dialogData.id}}</div>
        </el-form-item> -->
        <el-form-item label="充值类型">
          <span v-if="dialogData.configureType === 1">支付宝</span>
          <span v-if="dialogData.configureType === 2">银联</span>
        </el-form-item>
        <el-form-item label="最低充值金额">
          <div>{{dialogData.minPrice}}</div>
        </el-form-item>
        <el-form-item label="最高充值金额">
          <div>{{dialogData.maxPrice}}</div>
        </el-form-item>
        <el-form-item label="充值赠送比例">
          <div>{{dialogData.gaveRatio}}</div>
        </el-form-item>
        <el-form-item label="支付账号" v-if="dialogData.configureType === 1">
          <div>{{dialogData.payAccount}}</div>
        </el-form-item>
        <el-form-item label="支付昵称" v-if="dialogData.configureType === 1">
          <div>{{dialogData.payName}}</div>
        </el-form-item>
        <el-form-item label="银行卡名称" v-if="dialogData.configureType === 2">
          <div>{{dialogData.bankName}}</div>
        </el-form-item>
        <el-form-item label="银行卡卡号" v-if="dialogData.configureType === 2">
          <div>{{dialogData.bankCardNo}}</div>
        </el-form-item>
        <el-form-item label="银行卡持卡人" v-if="dialogData.configureType === 2">
          <div>{{dialogData.cardHolder}}</div>
        </el-form-item>
        <el-form-item label="开户行" v-if="dialogData.configureType === 2">
          <div>{{dialogData.openingBank}}</div>
        </el-form-item>
        <el-form-item label="状态">
          <span v-if="dialogData.status === 1">正常</span>
          <span v-if="dialogData.status === 2">禁用</span>
        </el-form-item>
        <el-form-item label="创建人">
          {{dialogData.createSysUserName}}
        </el-form-item>
        <el-form-item label="创建时间">
          {{dialogData.createDate}}
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import {rechargeInfoList, rechargeInfoAdd, rechargeInfoEdit, RechargeConfigureChoice, rechargeInfoInfo} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      dialogFormVisible: false,
      dialogFormVisible2: false,
      form: {configureId: '', payAccount: '', payName: '', bankName: '', bankCardNo: '', cardHolder: '', openingBank: ''},
      choseArr: [],
      totalNum: 0,
      serachData: {payAccount: '', payName: '', bankName: '', bankCardNo: '', cardHolder: '', openingBank: '', status: '', pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      checkedId: '',
      checked: [],
      options: [{value: 1, label: '正常'}, {value: 2, label: '禁用'}],
      options2: [],
      advertIds: [],
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._rechargeInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._rechargeInfoList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async detailsFn (row) {
      const res = await rechargeInfoInfo({id: row.id})
      if (res.result === '0') {
        this.dialogData = res.datas
        this.dialogFormVisible2 = true
      }
    },
    async _rechargeInfoList () {
      const res = await rechargeInfoList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.rechargeInfoList
      }
    },
    async _RechargeConfigureChoice () {
      const res = await RechargeConfigureChoice({})
      if (res.result === '0') {
        this.options2 = res.datas.rechargeConfigureList.map(item => {
          if (item.configureType === 1) item.configureType = '支付宝'
          else if (item.configureType === 2) item.configureType = '银联'
          return item
        })
      }
    },
    addRole () {
      this._RechargeConfigureChoice()
      this.dialogFormVisible = true
      this.setReset()
    },
    async submit () {
      if (this.form.configureId === '') {
        this.$message({type: 'warning', message: '请选择充值配置ID'})
      } else if (this.form.configureId === 1 && this.form.payAccount === '') {
        this.$message({type: 'warning', message: '请输入支付账号'})
      } else if (this.form.configureId === 1 && this.form.payName === '') {
        this.$message({type: 'warning', message: '请输入支付昵称'})
      } else if (this.form.configureId === 2 && this.form.bankName === '') {
        this.$message({type: 'warning', message: '请输入银行卡名称'})
      } else if (this.form.configureId === 2 && this.form.bankCardNo === '') {
        this.$message({type: 'warning', message: '请输入银行卡卡号'})
      } else if (this.form.configureId === 2 && this.form.cardHolder === '') {
        this.$message({type: 'warning', message: '请输入银行卡持卡人'})
      } else if (this.form.configureId === 2 && this.form.openingBank === '') {
        this.$message({type: 'warning', message: '请输入开户行'})
      } else {
        const res = await rechargeInfoAdd(this.form)
        if (res.result === '1') {
          this._rechargeInfoList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    async _freeze (row) {
      const res = await rechargeInfoEdit({status: row, id: this.choseArr[0].id})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.advertIds = []
        this._rechargeInfoList()
      }
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一条充值配置进行操作!'})
      } else {
        let tip
        row === 1 ? tip = '此操作将启用该充值配置，是否继续？' : row === 2 ? tip = '此操作将禁用该充值配置，是否继续？' : tip = '此操作将删除该充值配置，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {configureId: '', payAccount: '', payName: '', bankName: '', bankCardNo: '', cardHolder: '', openingBank: ''}
    },
    refresh () {
      this.serachData = {payAccount: '', payName: '', bankName: '', bankCardNo: '', cardHolder: '', openingBank: '', status: '', pageNum: 1, pageSize: 20}
      this._rechargeInfoList()
    },
    search () {
      this.serachData.pageNum = 1
      this._rechargeInfoList()
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._rechargeInfoList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
}
.editor-container {
  max-height: 500px;
  margin: 0 0 30px;
  .editor-upload-btn-container {
    text-align: right;
    margin-right: 10px;
    .editor-upload-btn {
      display: inline-block;
    }
  }
}
</style>



