<template>
  <div class="con">
    <el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px" class="demo-ruleForm" size="mini">
      <el-form-item label="一级比例" prop="oneRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.oneRatio"></el-input>
      </el-form-item>
      <el-form-item label="二级比例" prop="twoRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.twoRatio"></el-input>
      </el-form-item>
      <el-form-item label="三级比例" prop="threeRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.threeRatio"></el-input>
      </el-form-item>
      <el-form-item label="四级比例" prop="fourRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.fourRatio"></el-input>
      </el-form-item>
      <el-form-item label="五级比例" prop="fiveRatio">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.fiveRatio"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {gameJlWaterConfigJlWaterConfigInfo, gameJlWaterConfigUpdateJlWaterConfig} from '@/api'
import {checkSmallNumber} from '@/utils/commom'
export default {
  data () {
    return {
      ruleForm: {
        jlWaterConfigId: '',
        oneRatio: '',
        twoRatio: '',
        threeRatio: '',
        fourRatio: '',
        fiveRatio: ''
      },
      rules: {
        oneRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ],
        twoRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ],
        threeRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ],
        fourRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ],
        fiveRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    async _gameJlWaterConfigJlWaterConfigInfo () {
      const res = await gameJlWaterConfigJlWaterConfigInfo({})
      if (res.result === '0') {
        this.ruleForm.jlWaterConfigId = res.datas.id
        this.ruleForm.oneRatio = res.datas.oneRatio
        this.ruleForm.twoRatio = res.datas.twoRatio
        this.ruleForm.threeRatio = res.datas.threeRatio
        this.ruleForm.fourRatio = res.datas.fourRatio
        this.ruleForm.fiveRatio = res.datas.fiveRatio
      }
    },
    async submitForm () {
      if (this.ruleForm.oneRatio === '') {
        this.$message({type: 'warning', message: '请输入一级比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.ruleForm.oneRatio)) {
        this.$message({type: 'warning', message: '一级比例请输入非负小数'})
      } else if (this.ruleForm.twoRatio === '') {
        this.$message({type: 'warning', message: '请输入二级比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.ruleForm.twoRatio)) {
        this.$message({type: 'warning', message: '二级比例请输入非负小数'})
      } else if (this.ruleForm.threeRatio === '') {
        this.$message({type: 'warning', message: '请输入三级比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.ruleForm.threeRatio)) {
        this.$message({type: 'warning', message: '三级比例请输入非负小数'})
      } else if (this.ruleForm.fourRatio === '') {
        this.$message({type: 'warning', message: '请输入四级比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.ruleForm.fourRatio)) {
        this.$message({type: 'warning', message: '四级比例请输入非负小数'})
      } else if (this.ruleForm.fiveRatio === '') {
        this.$message({type: 'warning', message: '请输入五级比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.ruleForm.fiveRatio)) {
        this.$message({type: 'warning', message: '五级比例请输入非负小数'})
      } else {
        await gameJlWaterConfigUpdateJlWaterConfig(this.ruleForm)
      }
    }
  },
  created () {
    this._gameJlWaterConfigJlWaterConfigInfo()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
}
</style>



