<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">房间名称：</span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">玩家昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item three">
        <span class="title">邀请人：</span>
        <el-input v-model="requestData.invitationUserName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">房间状态：</span>
        <el-select clearable v-model="requestData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="pass">审核通过</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column prop="id" label="Id" width="80"></el-table-column>
        <el-table-column prop="userName" label="玩家昵称"></el-table-column>
        <el-table-column prop="roomName" label="房间名称"></el-table-column>
        <el-table-column prop="invitationUserName" label="邀请人名称"></el-table-column>
        <el-table-column prop="status" label="房间状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: #666">申请中</span>
            <span v-if="scope.row.status===5" style="color: #eb453c">禁用</span>
            <span v-if="scope.row.status===2" style="color: green">正常</span>
            <span v-if="scope.row.status===3" style="color: #eb453c">审核驳回</span>
            <span v-if="scope.row.status===4" style="color: #eb453c">退出</span>
            <span v-if="scope.row.status===6" style="color: #666">后台添加</span>
          </template>
        </el-table-column>
        <el-table-column prop="checkDate" label="审核时间"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status===1" type="primary" @click.native.prevent="nopass(scope.row)" size="mini">审核驳回</el-button>
            <el-button v-if="scope.row.status===2" type="primary" @click.native.prevent="freeze(scope.row, 5)" size="mini">禁用</el-button>
            <el-button v-if="scope.row.status===5" type="primary" @click.native.prevent="freeze(scope.row, 2)" size="mini">解禁</el-button>
            <el-button v-if="scope.row.status===6" type="primary" @click.native.prevent="freeze(scope.row, 7)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog title="审核备注" :visible.sync="dialogFormVisible2" center width="300px">
      <div class="plan-con">
        <div>
          <el-input type="textarea" placeholder="请输入审核备注" size="mini" v-model="checkRemark"></el-input>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false;">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlUserRoomList, gameSlUserRoomUpdateStatus, gameSlUserRoomCheck, gameSlUserRoomEeject} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, roomName: '', status: '', invitationUserName: '', userName: '', slRoomId: ''},
      ConfigList: [],
      count: 0,
      options: [{value: 1, label: '申请中'}, {value: 2, label: '正常'}, {value: 3, label: '审核驳回'}, {value: 4, label: '退出'}, {value: 5, label: '禁用'}, {value: 6, label: '后台添加'}],
      choseArr: [],
      userIds: [],
      haspass: false,
      dialogFormVisible2: false,
      checkRemark: ''
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.requestData.pageNum = val
      this._gameSlUserRoomList()
    },
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameSlUserRoomList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlUserRoomList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, roomName: '', status: '', invitationUserName: '', userName: '', slRoomId: ''}
      this._gameSlUserRoomList()
    },
    async _gameSlUserRoomList () {
      const res = await gameSlUserRoomList(this.requestData)
      if (res.result === '0') {
        this.ConfigList = res.datas.userRoomList
        this.count = res.datas.totalRow
      }
    },
    freeze (row, type) {
      let tip
      type === 5 ? tip = '此操作将禁用该房间的使用，是否继续?' : type === 2 ? tip = '此操作将恢复该房间的使用，是否继续?' : tip = '此操作将删除该房间的使用，是否继续?'
      this.$confirm(tip, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._freeze(row, type)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '操作已取消'
        })
      })
    },
    async _freeze (row, type) {
      const res = await gameSlUserRoomUpdateStatus({status: type, id: row.id})
      if (res.result === '1') {
        this._gameSlUserRoomList()
      }
    },
    pass () {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一间房间'})
      } else {
        this.userIds = this.choseArr.map((item) => {
          if (item.status > 1) {
            this.haspass = true
          }
          return item.id
        })
        if (this.haspass) {
          this.$message({type: 'warning', message: '请勿选择已通过审核的房间'})
        } else {
          this.$confirm('该操作将批量审核通过玩家扫雷房间申请！是否继续？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.surepass()
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '操作已取消'
            })
          })
        }
      }
    },
    async surepass () {
      const res = await gameSlUserRoomCheck({id: this.userIds.join(',')})
      if (res.result === '1') this._gameSlUserRoomList()
    },
    nopass (row) {
      this.dialogFormVisible2 = true
      this.id = row.id
    },
    submit () {
      if (this.checkRemark === '') {
        this.$message({type: 'warning', message: '请输入审核备注'})
      } else {
        let tip
        tip = '操作将驳回审核，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const res = await gameSlUserRoomEeject({id: this.id, remark: this.checkRemark})
          if (res.result === '1') {
            this.dialogFormVisible2 = false
            this.checkRemark = ''
            this._gameSlUserRoomList()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    }
  },
  created () {
    this._gameSlUserRoomList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



