<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">商品名称：</span>
        <el-input v-model="serachData.goodsName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addGood">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit">编辑</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">冻结</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">恢复正常</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="goodsList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="goodsLogo" label="商品LOGO" width="110">
          <template slot-scope="scope">
            <img style="width: 90px" :src="baseUrl+scope.row.goodsLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="goodsName" label="商品名称" width="100"></el-table-column>
        <el-table-column prop="goodsInfo" label="商品详情" min-width="120"></el-table-column>
        <el-table-column prop="goodsNum" label="商品库存" width="100"></el-table-column>
        <el-table-column prop="goodsPayNum" label="商品销售量" width="100"></el-table-column>
        <el-table-column prop="goodsSaleNum" label="商品实际销售量" width="110"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">冻结</span>
            <span v-if="scope.row.status === 3" style="color:#999">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建人"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="modifySysUserName" label="修改人"></el-table-column>
        <el-table-column prop="modifyDate" label="修改时间"></el-table-column>
        <el-table-column label="操作" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click.native.prevent="detailsFn(scope.row)" >查看</el-button>
        </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.page"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑商城商品': '添加商城商品'" :visible.sync="dialogFormVisible" width="450px">
      <el-form :model="form" :rules="rules" label-width="90px" size="mini">
        <el-form-item  label="商品LOGO">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="商品名称">
          <el-input style="width:280px" v-model="form.goodsName" autocomplete="off"></el-input>
        </el-form-item>
         <el-form-item label="商品详情">
          <el-input type="textarea" :rows="3" style="width:280px" v-model="form.goodsInfo" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品库存" prop="goodsNum">
          <el-input style="width:280px" v-model="form.goodsNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品销售量" prop="goodsPayNum">
          <el-input style="width:280px" v-model="form.goodsPayNum" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品状态">
          <el-switch v-model="form.status" active-color="#13ce66" active-value="1" inactive-value="2" inactive-color="#ff4949" active-text="开启" inactive-text="禁用"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
     <el-dialog title="商城商品详情" :visible.sync="dialogFormVisible2" center width="500px">
      <el-form class="form" ref="form" label-width="110px" label-position="left" size="mini">
        <el-form-item label="商品ID">
          <div>{{dialogData.id}}</div>
        </el-form-item>
        <el-form-item label="商品Logo" prop="goodsLogo" width="100">
          <div> <img style="width: 90px" :src="baseUrl + dialogData.goodsLogo" @click="showImg(baseUrl + dialogData.goodsLogo)" alt=""></div>
        </el-form-item>
        <el-form-item label="商品名称">
          <div>{{dialogData.goodsName}}</div>
        </el-form-item>
        <el-form-item label="商品详情">
          <div>{{dialogData.goodsInfo}}</div>
        </el-form-item>
        <el-form-item label="商品库存">
          <div>{{dialogData.goodsNum}}</div>
        </el-form-item>
        <el-form-item label="商品销售量">
          <div>{{dialogData.goodsPayNum}}</div>
        </el-form-item>
        <el-form-item label="商品实际销售量">
          <div>{{dialogData.goodsSaleNum}}</div>
        </el-form-item>
        <el-form-item label="商品状态">
          <div>
            <span v-if="dialogData.status === 1" style="color:#67c23a">正常</span>
            <span v-if="dialogData.status === 2" style="color:#eb453c">冻结</span>
            <span v-if="dialogData.status === 3" style="color:#999">删除</span>
          </div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import {goodsList, goodsAdd, goodsEdit, goodsUpdateStatus, goodsInfo} from '@/api'
import {baseUrl, checkInt} from '@/utils/commom'
import MyUpload from '@/components/Upload'
export default {
  data () {
    return {
      dialogFormVisible: false,
      dialogFormVisible2: false,
      isedit: false,
      form: {goodsName: '', goodsLogo: '', goodsInfo: '', goodsNum: '', goodsPayNum: '', status: '1'},
      choseArr: [],
      id: [],
      dialogData: {},
      count: 1,
      serachData: {pageNum: 1, pageSize: 20, goodsName: ''},
      goodsList: [],
      baseUrl: '',
      option: {
        type: 1,
        file: [],
        count: 1
      },
      rules: {
        goodsNum: [
          { validator: checkInt, trigger: 'change' }
        ],
        goodsPayNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      }
    }
  },
  components: {
    MyUpload
  },
  methods: {
    handleCurrentChange (val) {
      this.serachData.pageNum = val
      this.getGoodsList()
    },
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this.getGoodsList()
    },
    async detailsFn (row) {
      const res = await goodsInfo({id: row.id})
      if (res.result === '0') {
        this.dialogData = res.datas
        this.dialogFormVisible2 = true
      }
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    freeze (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一款商品'})
      } else {
        this.id = this.choseArr.map((item) => {
          return item.id
        })
        let tip
        row === 1 ? tip = '此操作将批量解冻选择的商品，是否继续？' : row === 2 ? tip = '此操作将批量冻结选择的商品，是否继续？' : tip = '此操作将批量删除选择的商品，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    async _freeze (row) {
      const res = await goodsUpdateStatus({status: row, id: this.id.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.getGoodsList()
      }
    },
    async getGoodsList () {
      const res = await goodsList(this.serachData)
      if (res.result === '0') {
        this.goodsList = res.datas.goodsList
        this.count = res.datas.totalRow
      }
    },
    async edit (row) {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一个商品进行编辑'})
      } else {
        this.dialogFormVisible = true
        this.option.file = []
        this.form.id = this.choseArr[0].id
        this.form.goodsName = this.choseArr[0].goodsName
        this.form.goodsLogo = this.choseArr[0].goodsLogo
        this.form.goodsInfo = this.choseArr[0].goodsInfo
        this.form.goodsNum = this.choseArr[0].goodsNum
        this.form.goodsPayNum = this.choseArr[0].goodsPayNum
        this.form.status = String(this.choseArr[0].status)
        this.option.file.push({file: this.choseArr[0].goodsLogo})
        this.isedit = true
      }
    },
    async submit () {
      if (this.form.goodsLogo === '') {
        this.$message({type: 'warning', message: '请填写商品logo'})
      } else if (this.form.goodsName === '' && !this.isedit) {
        this.$message({type: 'warning', message: '请填写商品名'})
      } else if (this.form.goodsInfo === '') {
        this.$message({type: 'warning', message: '请填写商品详情信息'})
      } else if (this.form.goodsNum === '') {
        this.$message({type: 'warning', message: '请填写商品库存'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.form.goodsNum)) {
        this.$message({type: 'warning', message: '请输入正整数'})
      } else if (this.form.goodsPayNum === '') {
        this.$message({type: 'warning', message: '请填写商品销售量'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.form.goodsPayNum)) {
        this.$message({type: 'warning', message: '请输入正整数'})
      } else if (!this.isedit) {
        const res = await goodsAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this.serachData.pageNum = 1
          this.getGoodsList()
          this.setReset()
        }
      } else {
        const res = await goodsEdit(this.form)
        if (res.result === '1') {
          this.getGoodsList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addGood () {
      this.setReset()
      this.dialogFormVisible = true
      this.isedit = false
    },
    upfiles (file) {
      this.form.goodsLogo = file.files[0].file
    },
    updelete (file) {
      this.form.goodsLogo = ''
    },
    cancle () {
      this.dialogFormVisible = false
    },
    setReset () {
      this.option.file = []
      this.form = {goodsName: '', goodsLogo: '', goodsInfo: '', goodsNum: '', goodsPayNum: '', status: '1'}
    },
    refresh () {
      this.serachData = {goodsName: '', pageNum: 1, pageSize: 20}
      this.getGoodsList()
    },
    search () {
      this.serachData.pageNum = 1
      this.getGoodsList()
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this.getGoodsList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
  .el-dialog__body .my-table{
    height: 350px;
  }
}
</style>



