<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">玩家名称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label="玩家名称"></el-table-column>
        <el-table-column prop="receivePrice" label="领包金额"></el-table-column>
        <el-table-column prop="collarReward" label="领包奖励"></el-table-column>
        <el-table-column prop="validNum" label="玩家当前余额"></el-table-column>
        <el-table-column prop="rewardDate" label="奖励时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>玩家名称</label>{{dialogData.userName}}</p>
        <p><label>领包金额</label>{{dialogData.receivePrice}}</p>
        <p><label>领包奖励</label>{{dialogData.collarReward}}</p>
        <p><label>玩家当前余额</label>{{dialogData.validNum}}</p>
        <p><label>奖励时间</label>{{dialogData.rewardDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlCollarInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: ''},
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameSlCollarInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._gameSlCollarInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlCollarInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._gameSlCollarInfoList()
    },
    async _gameSlCollarInfoList () {
      const res = await gameSlCollarInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.collarInfoList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameSlCollarInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



