
const routeArr = {
  state: {
    currentPage: '',
    loading: false,
    turnPage: false,
    current: '',
    routerMenu: [],
    showImg: { src: '', previewShow: false }
  },
  mutations: {
    GET_CURRENT_PAGE: (state, pageName) => {
      state.currentPage = pageName
    },
    UPDATE_LOADING: (state, loading) => {
      state.loading = loading
    },
    TURN_PAGE: (state, loading) => {
      state.turnPage = loading
    },
    ROUTER_MENU: (state, menu) => {
      state.routerMenu = menu
    },
    SHOW_IMG: (state, showImg) => {
      state.showImg.src = showImg.src
      state.showImg.previewShow = showImg.previewShow
    }
  },
  actions: {
    getCurrentPage ({commit}, pageName) {
      commit('GET_CURRENT_PAGE', pageName)
    },
    updateLoading ({commit}, loading) {
      commit('UPDATE_LOADING', loading)
    },
    turnPage ({commit}, loading) {
      commit('TURN_PAGE', loading)
    },
    routerMenu ({commit}, menu) {
      commit('ROUTER_MENU', menu)
    },
    updateShow ({commit}, showImg) {
      commit('SHOW_IMG', showImg)
    }
  }
}
export default routeArr



