<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item search-id-input">
        <span class="title">玩家ID ：</span>
        <el-input v-model="requestData.userId" clearable></el-input>
      </div>
      <div class="search-item search-phone-input">
        <span class="title">玩家手机：</span>
        <el-input v-model="requestData.phone" clearable ></el-input>
      </div>
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <el-tabs type="border-card" style="height: calc(100% - 40px);" @tab-click="handleClick" v-model="type">
      <el-tab-pane label="待审核" name="1">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userId" label="玩家ID"></el-table-column>
            <el-table-column prop="phone" label="玩家手机"></el-table-column>
            <el-table-column prop="userName" label="昵称" width="100"></el-table-column>
            <el-table-column prop="type" label="提现类型" width="90">
              <template slot-scope="scope">
                <span v-if="scope.row.type===1">银行卡</span>
                <span v-if="scope.row.type===2">支付宝</span>
              </template>
            </el-table-column>
            <el-table-column prop="withdrawPrice" label="提现金额" width="100"></el-table-column>
            <el-table-column prop="poundageRatio" label="手续费率" width="100"></el-table-column>
            <el-table-column prop="poundagePrice" label="手续费" width="100"></el-table-column>
            <el-table-column prop="actualWithdrawPrice" label="实际金额" width="100"></el-table-column>
            <el-table-column prop="remark" label="备注" min-width="100"></el-table-column>
            <el-table-column prop="status" label="状态" width="90">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1">申请中</span>
                <span v-if="scope.row.status===2">提现成功</span>
                <span v-if="scope.row.status===3">提现失败</span>
              </template>
            </el-table-column>
            <el-table-column prop="createDate" label="申请时间" width="150"></el-table-column>
            <el-table-column prop="registerDate" fixed="right" label="操作" width="300">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="submit(1, scope.row.id)">审核通过</el-button>
                <el-button size="mini" type="primary" @click="open(2, scope.row.id)">审核驳回</el-button>
                <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="已审核" name="2">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userId" label="用户ID" width="100"></el-table-column>
            <el-table-column prop="userName" label="昵称" width="100"></el-table-column>
            <el-table-column prop="type" label="提现类型" width="90">
              <template slot-scope="scope">
                <span v-if="scope.row.type===1">银行卡</span>
                <span v-if="scope.row.type===2">支付宝</span>
              </template>
            </el-table-column>
            <el-table-column prop="withdrawPrice" label="提现金额" width="100"></el-table-column>
            <el-table-column prop="poundageRatio" label="手续费率" width="100"></el-table-column>
            <el-table-column prop="poundagePrice" label="手续费" width="100"></el-table-column>
            <el-table-column prop="actualWithdrawPrice" label="实际金额" width="100"></el-table-column>
            <el-table-column prop="remark" label="备注" min-width="100"></el-table-column>
            <el-table-column prop="status" label="状态" width="90">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1">申请中</span>
                <span v-if="scope.row.status===2">提现成功</span>
                <span v-if="scope.row.status===3">提现失败</span>
              </template>
            </el-table-column>
            <el-table-column prop="checkSysUserName" label="审核人名称"></el-table-column>
            <el-table-column prop="checkStatus" label="审核结论">
              <template slot-scope="scope">
                <span v-if="scope.row.checkStatus===1">待审核</span>
                <span v-if="scope.row.checkStatus===2">审核成功</span>
                <span v-if="scope.row.checkStatus===3">审核驳回</span>
              </template>
            </el-table-column>
            <el-table-column prop="checkRemark" label="审核备注"></el-table-column>
            <el-table-column prop="checkDate" label="审核时间" width="150"></el-table-column>
            <el-table-column prop="createDate" label="申请时间" width="150"></el-table-column>
            <el-table-column prop="registerDate" fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>用户ID</label>{{dialogData.userId}}</p>
        <p><label>昵称</label>{{dialogData.userName}}</p>
        <p><label>提现类型</label><span v-if="dialogData.type===1">银行卡</span><span v-if="dialogData.type===2">支付宝</span></p>
        <p><label>提现金额</label>{{dialogData.withdrawPrice}}</p>
        <p><label>手续费率</label>{{dialogData.poundageRatio}}</p>
        <p><label>手续费</label>{{dialogData.poundagePrice}}</p>
        <p><label>实际金额</label>{{dialogData.actualWithdrawPrice}}</p>
        <p><label>备注</label>{{dialogData.remark}}</p>
        <p><label>状态</label><span v-if="dialogData.status===1">申请中</span><span v-if="dialogData.status===2">申请成功</span><span v-if="dialogData.status===3">申请失败</span></p>
        <p><label>审核人名称</label>{{dialogData.checkSysUserName}}</p>
        <p><label>审核结论</label><span v-if="dialogData.checkStatus===1">待审核</span><span v-if="dialogData.checkStatus===2">审核成功</span><span v-if="dialogData.checkStatus===3">审核驳回</span></p>
        <p><label>审核备注</label>{{dialogData.checkRemark}}</p>
        <p><label>审核时间</label>{{dialogData.checkDate}}</p>
        <p><label>申请时间</label>{{dialogData.createDate}}</p>
        <p v-if="dialogData.type===1"><label>银行信息</label><img style="width: 25px;vertical-align: middle;" :src="baseUrl + dialogData.bankLogo" @click="showImg(baseUrl + dialogData.bankLogo)" alt="">{{dialogData.bankName}}</p>
        <p v-if="dialogData.type===1"><label>持卡人</label>{{dialogData.cardHolder}}</p>
        <p v-if="dialogData.type===1"><label>银行卡卡号</label>{{dialogData.bankCardNo}}</p>
        <p v-if="dialogData.type===1"><label>开户行</label>{{dialogData.openingBank}}</p>
        <p v-if="dialogData.type===1"><label>银行简称</label>{{dialogData.bankCode}}</p>
        <p v-if="dialogData.type===2"><label>支付昵称</label>{{dialogData.aliPayName}}</p>
        <p v-if="dialogData.type===2"><label>支付账号</label>{{dialogData.aliPayNo}}</p>
      </div>
    </el-dialog>
    <el-dialog title="审核备注" :visible.sync="dialogFormVisible2" center width="300px">
      <div class="plan-con">
        <div>
          <el-input type="textarea" placeholder="请输入审核备注" size="mini" v-model="checkRemark"></el-input>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false;">取 消</el-button>
        <el-button type="primary" @click="submit(2)">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {userWithdrawList, userWithdrawInfo, userWithdrawUpdateStatus} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      requestData: {pageNum: 1, pageSize: 20, userName: '', type: '1', userId: '', phone: ''},
      options: [{value: 1, label: '充值'}, {value: 2, label: '提现申请'}, {value: 3, label: '提现失败'}, {value: 4, label: '转账支出'}, {value: 5, label: '转账收入'}, {value: 4, label: '商品兑换'}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      dialogFormVisible2: false,
      checkRemark: '',
      id: '',
      type: '1'
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._userWithdrawList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._userWithdrawList()
    },
    handleClick (val) {
      this.type = val.name
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', type: val.name}
      this._userWithdrawList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userWithdrawList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', type: this.type}
      this._userWithdrawList()
    },
    async _userWithdrawList () {
      const res = await userWithdrawList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    async detailsFn (row) {
      const res = await userWithdrawInfo({withdrawId: row.id})
      if (res.result === '0') {
        this.dialogData = res.datas
      }
      this.dialogFormVisible = true
    },
    open (s, id) {
      this.temp = s
      this.id = id
      this.dialogFormVisible2 = true
    },
    submit (temp, id) {
      this.temp = temp
      if (temp === 2 && this.checkRemark === '') {
        this.$message({type: 'warning', message: '请输入审核备注'})
      } else {
        let tip
        this.temp === 2 ? tip = '操作将驳回审核，是否继续？' : tip = '此操作将通过审核，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const res = await userWithdrawUpdateStatus({withdrawId: temp === 2 ? this.id : id, checkStatus: this.temp, checkRemark: this.checkRemark})
          if (res.result === '1') {
            this.dialogFormVisible = false
            this.dialogFormVisible2 = false
            this.checkRemark = ''
            this._userWithdrawList()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._userWithdrawList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>
<style>
.el-tabs--border-card>.el-tabs__content{
  height: calc(100% - 50px);
}
.el-tab-pane{
  height: 100%;
}
</style>



