<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item three">
        <span class="title">手机号：</span>
        <el-input v-model="requestData.phone" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">冻结</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">恢复正常</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="userDataList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          fixed>
        </el-table-column>
        <el-table-column prop="id" label="玩家Id" width="80" fixed></el-table-column>
        <el-table-column prop="bankName" label="玩家信息" min-width="180" fixed>
          <template slot-scope="scope">
            <div style="float:left;">
              <img style="display: inline-block;width: 30px;vertical-align: middle;" :src="scope.row.userLogo" alt="">
              {{scope.row.userName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="attestationType" label="性别" width="80">
          <template slot-scope="scope">
            <span v-if="scope.row.sex === 1">男</span>
            <span v-if="scope.row.sex === 2">女</span>
            <span v-if="scope.row.sex === 3">保密</span>
          </template>
        </el-table-column>
        <el-table-column prop="phone" label="手机号" width="100"></el-table-column>
        <el-table-column prop="countryName" label="所在地区" width="150">
          <template slot-scope="scope">
            {{scope.row.countryName + scope.row.provinceName + scope.row.cityName}}
          </template>
        </el-table-column>
        <el-table-column prop="trueName" label="真实姓名" width="90"></el-table-column>
        <el-table-column prop="wechatName" label="绑定微信" width="100"></el-table-column>
        <el-table-column prop="qqName" label="绑定qq" width="100"></el-table-column>
        <el-table-column prop="parentName" label="邀请人名称" width="90"></el-table-column>
        <el-table-column prop="qqName" label="状态" width="80">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">正常</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">禁用</span>
            <span v-if="scope.row.status===3" style="color: #666">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="accountPrice" label="账户余额" width="120"></el-table-column>
        <el-table-column prop="freezePrice" label="冻结金额" width="120"></el-table-column>
        <el-table-column prop="totalRechargePrice" label="累计充值金额" width="120"></el-table-column>
        <el-table-column prop="totalWithdrawPrice" label="累计提现金额" width="120"></el-table-column>
        <el-table-column prop="totalTransferPrice" label="累计转账金额" width="120"></el-table-column>
        <el-table-column prop="registerDate" label="注册时间" width="150"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="330">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">详情</el-button>
            <el-button type="primary" @click.native.prevent="_friendList(scope.row)" size="mini">好友列表</el-button>
            <el-button type="primary" @click.native.prevent="toRecharge(scope.row)" size="mini">上分</el-button>
            <el-button type="primary" @click.native.prevent="toWithdraw(scope.row)" size="mini">下分</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="用户详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>用户Id</label>{{dialogData.id}}</p>
        <p><label>手机号</label>{{dialogData.phone}}</p>
        <p><label>状态</label><span v-if="dialogData.status===1" style="color: green">正常</span><span v-if="dialogData.status===2" style="color: #eb453c">禁用</span><span v-if="dialogData.status===3" style="color: #666">删除</span></p>
        <p><label>昵称</label>{{dialogData.userName}}</p>
        <p><label>头像</label><img :src="dialogData.userLogo" @click="showImg(dialogData.userLogo)" alt=""></p>
        <p><label>性别</label><span v-if="dialogData.sex === 1">男</span><span v-if="dialogData.sex === 2">女</span><span v-if="dialogData.sex === 3">保密</span></p>
        <p><label>真实姓名</label>{{dialogData.trueName}}</p>
        <p><label>所在地区</label>{{dialogData.countryName+dialogData.provinceName+dialogData.cityName}}</p>
        <p><label>微信昵称</label>{{dialogData.wechatName}}</p>
        <p><label>qq昵称</label>{{dialogData.qqName}}</p>
        <p><label>邀请人名称</label>{{dialogData.parentName}}</p>
        <p><label>账户余额</label>{{dialogData.accountPrice}}</p>
        <p><label>冻结金额</label>{{dialogData.freezePrice}}</p>
        <p><label>累计充值金额</label>{{dialogData.totalRechargePrice}}</p>
        <p><label>累计提现金额</label>{{dialogData.totalWithdrawPrice}}</p>
        <p><label>累计转账金额</label>{{dialogData.totalTransferPrice}}</p>
        <p><label>注册时间</label>{{dialogData.registerDate}}</p>
      </div>
    </el-dialog>
    <el-dialog :close-on-click-modal="false" title="用户好友列表" :visible.sync="dialogFormVisible2" center width="500px">
      <div class="search-zu">
        <div class="search-item four">
          <span class="title">好友名称：</span>
          <el-input v-model="dialogSearch.friendsName" clearable></el-input>
        </div>
        <el-button size="mini" type="primary" icon="el-icon-search" @click="search2">搜索</el-button>
      </div>
      <div class="my-table cl center">
        <el-table :data="friendsList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
          <el-table-column prop="friendsName" label="好友名称" width="100"></el-table-column>
          <el-table-column prop="remark" label="备注" width="100"></el-table-column>
          <el-table-column prop="status" label="状态">
            <template slot-scope="scope">
              <span v-if="scope.row.status===1" style="color: green">正常</span>
              <span v-if="scope.row.status===2" style="color: #eb453c">禁用</span>
              <span v-if="scope.row.status===3" style="color: #666">删除</span>
            </template>
          </el-table-column>
          <el-table-column prop="createDate" label="创建时间" width="150"></el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange1"
          @current-change="handleCurrentChange1"
          :current-page="dialogSearch.pageNum"
          :page-sizes="[20, 30, 40, 50]"
          :page-size="dialogSearch.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count">
        </el-pagination>
      </div>
    </el-dialog>
    <!-- 会员上分 -->
    <el-dialog title="上分" :visible.sync="dialogFormVisible3" width="350px">
      <el-form :model="userRecharge" :rules="rules" label-width="100px" size="mini">

        <el-form-item label="上分数量" prop="rechargeNum">
          <el-input style="width:193px" v-model="userRecharge.rechargeNum" placeholder="请输入上分数量" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input style="width:193px" v-model="userRecharge.remark" placeholder="备注" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2">取 消</el-button>
        <el-button type="primary" @click="submitRecharge">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 会员下分 -->
    <el-dialog title="下分" :visible.sync="dialogFormVisible4" width="350px">
      <el-form :model="userWithdraw" :rules="rules" label-width="100px" size="mini">

        <el-form-item label="下分数量" prop="withdrawNum">
          <el-input style="width:193px" v-model="userWithdraw.withdrawNum" placeholder="请输入下分数量" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input style="width:193px" v-model="userWithdraw.remark" placeholder="备注" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3">取 消</el-button>
        <el-button type="primary" @click="submitWithdraw">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import {userList, userUpdateUserStatus, userUserFriendsList, changeAccountRecharge, changeAccountWithdraw} from '@/api'
import {checkInt, checkDefeatInt} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', phone: ''},
      userDataList: [],
      choseArr: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogFormVisible3: false,
      dialogFormVisible4: false,
      userRecharge: {userId: '', remark: '', rechargeNum: ''},
      userWithdraw: {userId: '', remark: '', withdrawNum: ''},
      dialogData: {},
      userIds: [],
      dialogSearch: {friendsName: '', pageNum: 1, pageSize: 20, userId: ''},
      dialogFormVisible2: false,
      friendsList: [],
      count: 0,
      rules: {
        withdrawNum: [
          { validator: checkDefeatInt, trigger: 'change' }
        ],
        rechargeNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._userList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._userList()
    },
    handleSizeChange1 (val) { // 每页条数改变
      this.dialogSearch.pageSize = val
      this._friendList()
    },
    handleCurrentChange1 (val) { // page改变
      this.dialogSearch.pageNum = val
      this._friendList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userList()
    },
    search2 () {
      this.dialogSearch.pageNum = 1
      this._friendList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', phone: ''}
      this._userList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
      this.choseArr = val
    },
    async _userList () {
      const res = await userList(this.requestData)
      if (res.result === '0') {
        this.userDataList = res.datas.userList
        this.totalNum = res.datas.totalRow
      }
    },
    freeze (row) {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一个用户'})
      } else {
        this.userIds = this.choseArr.map((item) => {
          return item.id
        })
        let tip
        row === 1 ? tip = '此操作将批量解冻选择的用户，是否继续？' : row === 2 ? tip = '此操作将批量冻结选择的用户，是否继续？' : tip = '此操作将批量删除选择的用户，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    async _freeze (row) {
      const res = await userUpdateUserStatus({status: row, ids: this.userIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this._userList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    },
    async _friendList (row) {
      this.dialogFormVisible2 = true
      this.dialogSearch.userId = row.id
      const res = await userUserFriendsList(this.dialogSearch)
      if (res.result === '0') {
        this.friendsList = res.datas.list
        this.count = res.datas.totalRow
      }
    },
    toRecharge (row) {
      this.userRecharge.userId = row.id
      this.dialogFormVisible3 = true
    },
    toWithdraw (row) {
      this.userWithdraw.userId = row.id
      this.dialogFormVisible4 = true
    },
    // 上分取消
    cancle2 () {
      this.dialogFormVisible3 = false
      this.setReset2()
    },
    setReset2 () {
      this.userRecharge = {userId: '', remark: '', rechargeNum: ''}
    },
    // 下分取消
    cancle3 () {
      this.dialogFormVisible4 = false
      this.setReset3()
    },
    setReset3 () {
      this.userWithdraw = {userId: '', remark: '', withdrawNum: ''}
    },
    // 上分
    async submitRecharge () {
      if (this.userRecharge.rechargeNum === '') {
        this.$message({type: 'warning', message: '请输入上分数量'})
      } else if (this.userRecharge.rechargeNum !== '' && !/^[1-9]*[1-9][0-9]*$/.test(this.userRecharge.rechargeNum)) {
        this.$message({type: 'warning', message: '请输入正整数'})
      } else {
        const res = await changeAccountRecharge(this.userRecharge)
        if (res.result === '1') {
          this._userList()
          this.dialogFormVisible3 = false
          this.setReset2()
        }
      }
    },
    // 下分
    async submitWithdraw () {
      if (this.userWithdraw.withdrawNum === '') {
        this.$message({type: 'warning', message: '请输入下分数量'})
      } else if (this.userWithdraw.withdrawNum !== '' && !/^-[1-9]*[1-9][0-9]*$/.test(this.userWithdraw.withdrawNum)) {
        this.$message({type: 'warning', message: '请输入负整数'})
      } else {
        const res = await changeAccountWithdraw(this.userWithdraw)
        if (res.result === '1') {
          this._userList()
          this.dialogFormVisible4 = false
          this.setReset3()
        }
      }
    }
  },
  created () {
    this._userList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
.el-dialog .cl{
  height: 350px;
}
</style>



