<template>
  <div class="all-container index">
    <section class="content">
      <section class="menu-con" :class="{active: isCollapse}"><my-menu :isCollapse="isCollapse" @changeCollapse="changeCollapse" /></section>
      <section class="substance" :class="{active: isCollapse}">
        <!-- <div class="head">
          <span class="menu-drag" @click="isCollapse=!isCollapse" >
            <i v-if="!isCollapse" class="icon iconfont icon-shouqicaidan" title="收起"></i>
            <i v-else class="icon iconfont icon-zhankaicaidan" title="展开"></i>
          </span>
          <ul class="shortcut">
            <li :class="$route.name === 'dashboard' ? 'active' : ''"><router-link to="/dashboard"><i class="icon iconfont icon-shouye"></i>首页</router-link></li>
            <li :class="$route.name === 'LearningMaterials' ? 'active' : ''"><router-link to="/LearningMaterials"><i class="icon iconfont icon-yonghuming"></i>学习资料</router-link></li>
            <li :class="$route.name === 'CourseManage' ? 'active' : ''"><router-link to="/CourseManage"><i class="icon iconfont icon-jilu"></i>课程管理</router-link></li>
            <li :class="$route.name === 'PracticeManage' ? 'active' : ''">
              <router-link to="/PracticeManage"><i class="icon iconfont icon-19"></i>练习管理</router-link>
            </li>
            <li :class="$route.name === 'ExamManage' ? 'active' : ''">
              <router-link to="/ExamManage"><i class="icon iconfont icon-chongzhijilu"></i>考试管理</router-link>
            </li>
            <li :class="$route.name === 'ClerkAccount' ? 'active' : ''">
              <router-link to="/ClerkAccount"><i class="icon iconfont icon-liuyanjianyi"></i>店员账号管理</router-link>
            </li>
          </ul>
          <div class="dropdown">
            <el-dropdown @command="handleCommand">
              <span class="el-dropdown-link">
                {{msg.username}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="1">修改密码</el-dropdown-item>
                <el-dropdown-item command="2" divided>退出</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div> -->
        <top-head :isCollapse="isCollapse" @open="open" :username="msg.username"/>
        <section class="main">
          <tag-view />
          <transition name="fade-transform" mode="out-in">
            <div id="content" :key="key">
              <keep-alive :include="$route.keepAlive"><router-view/></keep-alive>
            </div>
          </transition>
        </section>
      </section>
    </section>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
// import {admininfo} from '@/api'
import MyMenu from './components/Menu'
import TopHead from './components/head'
import TagView from './components/targview'
export default {
  name: 'Index',
  data () {
    return {
      isCollapse: false,
      msg: {username: ''}
    }
  },
  computed: {
    ...mapGetters([
      'currentPage'
    ]),
    key () {
      return this.$route.fullPath
    }
  },
  components: {
    MyMenu,
    TopHead,
    TagView
  },
  methods: {
    changeCollapse (data) {
      this.isCollapse = data
    },
    open (val) {
      this.isCollapse = val
    }
  }
}
</script>

<style lang="less" scoped>
.head{
  height: 50px;
}
.content{
  height: 100%;
  .menu-con {
    height: 100%;
    transition: all 0.3s;
  }
  .substance{
    position: absolute;
    min-width: 900px;
    top: 0;
    left: 165px;
    bottom: 0px;
    right: 0;
    transition: all 0.3s;
    &.active{
      left: 50px;
    }
    .main{
      height: calc(100% - 50px);
      overflow-y: auto;
      overflow-x: hidden;
      background-color: #e2e9ef;
      .nav{
        background: #fff;
        padding-left: 30px;
        display: flex;
        height: 30px;
        align-items: center;
        overflow: hidden;
        .icon-weizhi{
          font-size: 18px;
          color: #eb453c;
          margin-right: 8px;
        }
      }
    }
  }
}
#content{
  margin: 10px;
  height: calc(100% - 70px);
  background: #fff;
  padding: 10px;
}
</style>

<style>
  .fade-transform-enter-active,.fade-transform-leave-active {
    transition: all 0.5s ease;
  }
  .fade-transform-enter{
    transform: translate3d(-30px,0,0);
    opacity: 0;
  }
  .fade-transform-leave-to{
    opacity: 0;
    transform: translate3d(10px,0,0);
  }
</style>



