<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">商品名称：</span>
        <el-input v-model="requestData.goodsName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="requestData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="goodsConvertList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="goodsConvertId" label="商品兑换Id" width="80"></el-table-column>
        <el-table-column prop="goodsName" label="商品名称" width="100"></el-table-column>
        <el-table-column prop="priceNum" label="商品花费金额" width="100"></el-table-column>
        <el-table-column prop="convertName" label="收货人姓名" width="100"></el-table-column>
        <el-table-column prop="convertPhone" label="收货人电话" width="100"></el-table-column>
        <el-table-column prop="convertAddress" label="收货人地址" width="100"></el-table-column>
        <el-table-column prop="userName" label="用户名称" width="100"></el-table-column>
        <el-table-column prop="createDate" label="创建时间" width="100"></el-table-column>
        <el-table-column prop="deliverSysUserName" label="发货人" width="100"></el-table-column>
        <el-table-column prop="deliverDate" label="发货时间" width="100"></el-table-column>
        <el-table-column prop="finishSysUserName" label="完成人" width="100"></el-table-column>
        <el-table-column prop="finishDate" label="完成时间" width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="80">
        <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: #eb453c">待处理</span>
            <span v-if="scope.row.status===2" style="color: green">已发货</span>
            <span v-if="scope.row.status===3" style="color: green">已完成</span>
        </template>
        </el-table-column>
        <el-table-column prop="createDate" label="创建时间" width="150"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作">
         <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="商品详情信息" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p>
          <el-button type="primary" size="mini" @click="editStatus(1)" :disabled="dialogData.status != '1'">发货</el-button>
          <el-button type="primary" size="mini" @click="editStatus(2)" :disabled="dialogData.status != '2'">完成</el-button>
        </p>
        <p><label>商品兑换Id</label>{{dialogData.goodsConvertId}}</p>
        <p><label>商品名称</label>{{dialogData.goodsName}}</p>
        <p>
          <label>状态</label>
          <span v-if="dialogData.status===1" style="color: #eb453c">待处理</span>
          <span v-if="dialogData.status===2" style="color: green">已发货</span>
          <span v-if="dialogData.status===3" style="color: green">已完成</span>
        </p>
        <p><label>花费金额</label>{{dialogData.priceNum}}</p>
        <p><label>收货人姓名</label>{{dialogData.convertName}}</p>
        <p><label>收货人电话</label>{{dialogData.convertPhone}}</p>
        <p><label>收货人地址</label>{{dialogData.convertAddress}}</p>
        <p><label>用户名称</label>{{dialogData.userName}}</p>
        <p><label>创建时间</label>{{dialogData.createDate}}</p>
        <p><label>发货人</label>{{dialogData.deliverSysUserName}}</p>
        <p><label>发货时间</label>{{dialogData.deliverDate}}</p>
        <p><label>完成人</label>{{dialogData.finishSysUserName}}</p>
        <p><label>完成时间</label>{{dialogData.finishDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {goodsConvertList, goodsConvertEditStatus} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, goodsName: '', status: ''},
      goodsConvertList: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      count: 0,
      options: [{value: 1, label: '待处理'}, {value: 2, label: '已发货'}, {value: 3, label: '已完成'}]
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._convertList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._convertList()
    },
    cancle () {
      this.dialogFormVisible = false
    },
    search () {
      this.requestData.pageNum = 1
      this._convertList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, goodsName: '', status: ''}
      this._convertList()
    },
    async _convertList () {
      const res = await goodsConvertList(this.requestData)
      if (res.result === '0') {
        this.goodsConvertList = res.datas.goodsConvertList
        this.totalNum = res.datas.totalRow
      }
    },
    async editStatus (type) {
      const res = await goodsConvertEditStatus({goodsConvertId: this.dialogData.goodsConvertId, type: type})
      if (res.result === '1') {
        this.dialogFormVisible = false
        this._convertList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this._convertList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



