<template>
  <div>
    <p class="title">个人密码修改</p>
    <el-form :model="ruleForm" ref="ruleForm" label-width="90px" class="demo-ruleForm" size="mini">
      <el-form-item label="旧密码" prop="oldPassword">
        <el-input type="password" style="width:190px;" v-model="ruleForm.oldPassword" placeholder="请输入旧密码"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="password">
        <el-input type="password" style="width:190px;" v-model="ruleForm.newPassword" placeholder="请输入新密码"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码" prop="checkpassword">
        <el-input type="password" style="width:190px;" v-model="checkpassword" placeholder="请确认新密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm()">保存</el-button>
        <el-button @click="resetForm()">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {editUserPwd} from '@/api'
import {removeToken} from '@/utils/auth'
export default {
  data () {
    return {
      ruleForm: {
        oldPassword: '',
        newPassword: ''
      },
      checkpassword: ''
    }
  },
  components: {
  },
  methods: {
    async submitForm () {
      if (this.ruleForm.oldPassword === '') {
        this.$message({type: 'warning', message: '请输入原始密码'})
      } else if (this.ruleForm.newPassword === '') {
        this.$message({type: 'warning', message: '请填写新密码'})
      } else if (this.checkpassword === '') {
        this.$message({type: 'warning', message: '请填写确认新密码'})
      } else if (this.checkpassword !== this.ruleForm.newPassword) {
        this.$message({type: 'warning', message: '请填写确认新密码'})
      } else {
        const res = await editUserPwd(this.ruleForm)
        if (res.result === '1') {
          this.$message({type: 'success', message: res.message})
          removeToken()
          this.$router.push('/Login')
        } else {
          this.$message({type: 'error', message: res.message})
          this.resetForm()
        }
      }
    },
    resetForm () {
      this.ruleForm = {oldPassword: '', newPassword: ''}
      this.checkpassword = ''
    }
  },
  created () {
  }
}
</script>

<style lang="less" scoped>
 .title{
  font-size: 16px;
  line-height: 50px;
 }
</style>



