<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item three">
        <span class="title">用户名：</span>
        <el-input v-model="serachData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">用户姓名：</span>
        <el-input v-model="serachData.trueName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="serachData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addUser">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit">编辑</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">冻结</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">恢复正常</el-button>
        <el-button size="mini" type="primary" @click="reset">重置密码</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="sysUser" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column prop="userName" label="用户名"></el-table-column>
        <el-table-column prop="trueName" label="用户姓名"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">冻结</span>
            <span v-if="scope.row.status === 3" style="color:#999">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="loginIp" label="登录IP"></el-table-column>
        <el-table-column prop="loginDate" label="登录时间"></el-table-column>
        <el-table-column prop="loginDate" label="操作" min-width="60">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="openRoleTable(scope.row.id)">分配角色</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.page"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑平台人员信息': '新增平台人员'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="80px" size="mini">
        <el-form-item v-if="!isedit" label="账号名">
          <el-input style="width:180px" v-model="form.userName" placeholder="请输入账号名" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item v-if="!isedit" label="密码">
          <el-input style="width:180px" v-model="form.password" placeholder="请输入密码" type="password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="账号姓名">
          <el-input style="width:180px" v-model="form.trueName" placeholder="请输入账号姓名" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="角色分配" :visible.sync="dialogFormVisible2" width="300px">
      <div class="my-table center">
        <el-table :data="roleList" ref="roletable" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange1">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column prop="roleName" label="角色名称"></el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false">取 消</el-button>
        <el-button type="primary" @click="sure">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {systemUserList, systemUserAdd, systemUserEdit, editStatus, resetPassword, userRoleList, userRoleAddUserRole} from '@/api'
export default {
  data () {
    return {
      dialogFormVisible: false,
      dialogFormVisible2: false,
      isedit: false,
      form: {userName: '', password: '', trueName: ''},
      choseArr: [],
      choserol: [],
      userIds: [],
      roleids: [],
      count: 1,
      serachData: {userName: '', status: '', pageNum: 1, trueName: '', pageSize: 20},
      sysUser: [],
      roleList: [],
      userid: '',
      options: [{value: 1, label: '正常'}, {value: 2, label: '冻结'}]
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.serachData.pageNum = val
      this.getUserList()
    },
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this.getUserList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    handleSelectionChange1 (val) {
      this.choserol = val
    },
    edit () {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一个用户进行编辑'})
      } else {
        this.dialogFormVisible = true
        this.form.trueName = this.choseArr[0].trueName
        this.form.id = this.choseArr[0].id
        this.isedit = true
      }
    },
    freeze (row) {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一个用户'})
      } else {
        this.userIds = this.choseArr.map((item) => {
          return item.id
        })
        let tip
        row === 1 ? tip = '此操作将批量解冻选择的用户，是否继续' : row === 2 ? tip = '此操作将批量冻结选择的用户，是否继续' : tip = '此操作将批量删除选择的用户，是否继续'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    reset () {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一个用户'})
      } else {
        this.userIds = this.choseArr.map((item) => {
          return item.id
        })
        this.$confirm('此操作将批量重置选择的用户的密码，是否继续', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._reset()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    openRoleTable (id) {
      this.userid = id
      this.dialogFormVisible2 = true
      this._sysRoleList()
    },
    async _sysRoleList () {
      const res = await userRoleList({userId: this.userid})
      this.$refs.roletable.clearSelection()
      this.choserol = []
      if (res.result === '0') {
        this.roleList = res.datas.list
        setTimeout(() => {
          res.datas.list.map((item) => {
            if (item.type === 'true') {
              this.choserol.push(item)
              this.$refs.roletable.toggleRowSelection(item)
            }
          })
        }, 20)
      }
    },
    async _freeze (row) {
      const res = await editStatus({type: row, userIds: this.userIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.getUserList()
      }
    },
    async _reset (row) {
      const res = await resetPassword({userIds: this.userIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
      }
    },
    async getUserList () {
      const res = await systemUserList(this.serachData)
      if (res.result === '0') {
        this.sysUser = res.datas.sysUser
        this.count = res.datas.totalRow
      }
    },
    async submit () {
      if (this.form.userName === '' && !this.isedit) {
        this.$message({type: 'warning', message: '请填写账号名'})
      } else if (this.form.password === '' && !this.isedit) {
        this.$message({type: 'warning', message: '请填写账号密码'})
      } else if (this.form.trueName === '') {
        this.$message({type: 'warning', message: '请填写账号姓名'})
      } else if (!this.isedit) {
        const res = await systemUserAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this.serachData.pageNum = 1
          this.getUserList()
          this.setReset()
        }
      } else {
        const res = await systemUserEdit({id: this.form.id, trueName: this.form.trueName})
        if (res.result === '1') {
          this.getUserList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addUser () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {userName: '', password: '', trueName: ''}
    },
    refresh () {
      this.serachData = {userName: '', status: '', pageNum: 1, trueName: '', pageSize: 20}
      this.getUserList()
    },
    search () {
      this.serachData.pageNum = 1
      this.getUserList()
    },
    async sure () {
      if (this.choserol.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一个要分配的角色'})
      } else {
        this.roleids = this.choserol.map((item) => {
          return item.id
        })
        const res = await userRoleAddUserRole({userId: this.userid, roleId: this.roleids.join(',')})
        if (res.result === '1') {
          this.dialogFormVisible2 = false
        }
      }
    }
  },
  created () {
    this.getUserList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
  .el-dialog__body .my-table{
    height: 350px;
  }
}
</style>



