<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="id" label="Id" width="80"></el-table-column>
        <el-table-column prop="awardType" label="奖品类型" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.awardType===1">金额</span>
            <span v-if="scope.row.awardType===2">实物</span>
            <span v-if="scope.row.awardType===3">谢谢惠顾</span>
          </template>
        </el-table-column>
        <el-table-column prop="awardName" label="奖品名称" width="100"></el-table-column>
        <el-table-column prop="awardPrice" label="奖品金额" width="100"></el-table-column>
        <el-table-column prop="winRatio" label="中奖比例" width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">正常</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">禁用</span>
            <span v-if="scope.row.status===3" style="color: #eb453c">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="modifySysUserName" label="修改人" width="90"></el-table-column>
        <el-table-column prop="modifyDate" label="修改时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">修改抽奖配置</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="修改中雷配置" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" :rules="rules" label-width="80px" size="mini">
        <el-form-item label="奖品类型">
          <el-select v-model="form.awardType" placeholder="请选择奖品类型">
            <el-option label="金额" value="1"></el-option>
            <el-option label="实物" value="2"></el-option>
            <el-option label="谢谢惠顾" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="奖品名称">
          <el-input style="width:180px" v-model="form.awardName" placeholder="请输入中雷数量" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="中奖比例" prop="winRatio">
          <el-input type="number" style="width:180px" v-model="form.winRatio" placeholder="请输入中雷奖励" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="奖品金额" prop="awardPrice">
          <el-input type="number" style="width:180px" v-model="form.awardPrice" placeholder="请输入中雷奖励" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlAwardConfigSlAwardConfigList, gameSlAwardConfigUpdateSlAwardConfig} from '@/api'
import {checkSmallNumber, checkPic} from '@/utils/commom'
export default {
  data () {
    return {
      ConfigList: [],
      dialogFormVisible: false,
      form: {awardType: '', awardName: '', winRatio: '', awardPrice: '', slAwardConfigId: ''},
      rules: {
        winRatio: [
          { validator: checkSmallNumber, trigger: 'change' }
        ],
        awardPrice: [
          { validator: checkPic, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    refresh () {
      this._gameSlAwardConfigSlAwardConfigList()
    },
    async _gameSlAwardConfigSlAwardConfigList () {
      const res = await gameSlAwardConfigSlAwardConfigList({})
      if (res.result === '0') {
        this.ConfigList = res.datas.list
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.form.awardType = String(row.awardType)
      this.form.awardName = row.awardName
      this.form.winRatio = row.winRatio
      this.form.awardPrice = row.awardPrice
      this.form.slAwardConfigId = row.id
    },
    async submit () {
      if (this.form.awardType === '') {
        this.$message({type: 'warning', message: '请选择奖品类型'})
      } else if (this.form.awardName === '') {
        this.$message({type: 'warning', message: '请输入奖品名称'})
      } else if (this.form.winRatio === '') {
        this.$message({type: 'warning', message: '请输入中奖比例'})
      } else if (!/^(([0-9]+|0)\.([0-9]{1,}))$/.test(this.form.winRatio)) {
        this.$message({type: 'warning', message: '请输入非负小数'})
      } else if (this.form.awardPrice === '') {
        this.$message({type: 'warning', message: '请输入奖品金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.awardPrice)) {
        this.$message({type: 'warning', message: '请输入正确的奖品金额'})
      } else {
        const res = await gameSlAwardConfigUpdateSlAwardConfig(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this._gameSlAwardConfigSlAwardConfigList()
        }
      }
    }
  },
  created () {
    this._gameSlAwardConfigSlAwardConfigList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 40px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



