<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addConfig">新增</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="id" label="Id" width="80"></el-table-column>
        <el-table-column prop="collarPrice" label="领包金额" width="100"></el-table-column>
        <el-table-column prop="collarReward" label="领包奖励" width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">正常</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">禁用</span>
            <span v-if="scope.row.status===3" style="color: #666">删除</span>
          </template>
        </el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="modifySysUserName" label="修改人" width="90"></el-table-column>
        <el-table-column prop="modifyDate" label="修改时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="270">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">修改领包配置</el-button>
            <el-button v-if="scope.row.status===1" type="primary" @click.native.prevent="freeze(scope.row, 1)" size="mini">禁用</el-button>
            <el-button v-else type="primary" @click.native.prevent="freeze(scope.row, 2)" size="mini">解禁</el-button>
            <el-button type="primary" @click.native.prevent="freeze(scope.row, 3)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑领包配置': '新增领包配置'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" :rules="rules" label-width="80px" size="mini">
        <el-form-item label="领包金额" prop="collarPrice">
          <el-input style="width:180px" v-model="form.collarPrice" placeholder="请输入领包金额" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="领包奖励" prop="collarReward">
          <el-input style="width:180px" v-model="form.collarReward" placeholder="请输入领包奖励" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlCollarConfigList, gameSlCollarConfigUpdateStatus, gameSlCollarConfigEdit, gameSlCollarConfigAdd} from '@/api'
import {checkPic} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20},
      ConfigList: [],
      dialogFormVisible: false,
      form: {collarPrice: '', collarReward: ''},
      count: 0,
      isedit: false,
      rules: {
        collarPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        collarReward: [
          { validator: checkPic, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.requestData.pageNum = val
      this._gameSlCollarConfigList()
    },
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameSlCollarConfigList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20}
      this._gameSlCollarConfigList()
    },
    async _gameSlCollarConfigList () {
      const res = await gameSlCollarConfigList(this.requestData)
      if (res.result === '0') {
        this.ConfigList = res.datas.collarConfigList
        this.count = res.datas.totalRow
      }
    },
    freeze (row, type) {
      let tip
      type === 1 ? tip = '此操作将禁用该领包配置，是否继续' : type === 2 ? tip = '此操作将解禁该领包配置，是否继续' : tip = '此操作将删除该领包配置，是否继续'
      this.$confirm(tip, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._freeze(row, type)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '操作已取消'
        })
      })
    },
    async _freeze (row, type) {
      let status = type === 1 ? 2 : type === 2 ? 1 : 3
      const res = await gameSlCollarConfigUpdateStatus({status: status, id: row.id})
      if (res.result === '1') {
        this._gameSlCollarConfigList()
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.form.id = row.id
      this.form.collarPrice = row.collarPrice
      this.form.collarReward = row.collarReward
      this.isedit = true
    },
    addConfig () {
      this.dialogFormVisible = true
      this.form = {collarPrice: '', collarReward: ''}
      this.isedit = false
    },
    async submit () {
      if (this.form.collarPrice === '') {
        this.$message({type: 'warning', message: '请输入领包金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.collarPrice)) {
        this.$message({type: 'warning', message: '请输入正确的领包金额'})
      } else if (this.form.collarReward === '') {
        this.$message({type: 'warning', message: '请输入领包奖励'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.collarReward)) {
        this.$message({type: 'warning', message: '请输入正确的奖励金额'})
      } else {
        let res
        this.isedit ? res = await gameSlCollarConfigEdit(this.form) : res = await gameSlCollarConfigAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this._gameSlCollarConfigList()
        }
      }
    }
  },
  created () {
    this._gameSlCollarConfigList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



