<!-- 牛牛房间 -->
<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">房间名称：</span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">房间状态：</span>
        <el-select clearable v-model="requestData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="search-item five">
        <span class="title">机器人状态：</span>
        <el-select clearable v-model="requestData.robotStatus" placeholder="请选择">
          <el-option v-for="(item, index) in options1" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addConfig">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="nnRoomId" label="ID" width="80"></el-table-column>
        <el-table-column prop="roomName" label="房间名称" width="100"></el-table-column>
        <el-table-column prop="roomLogo" label="房间LOGO" width="110">
          <template slot-scope="scope">
            <img style="width:90px" :src="baseUrl + scope.row.roomLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="roomNotice" label="房间公告"></el-table-column>
        <el-table-column prop="minPrice" label="下注最小金额" width="130"></el-table-column>
        <el-table-column prop="maxPrice" label="下注最大金额" width="130"></el-table-column>
        <el-table-column prop="roomStatus" label="房间状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.roomStatus===1" style="color: green">正常</span>
            <span v-if="scope.row.roomStatus===2" style="color: #eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="robotStatus" label="机器人状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.robotStatus===1" style="color: green">正常</span>
            <span v-if="scope.row.robotStatus===2" style="color: #eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="maxPeopleNum" label="最大下注人数" width="130"></el-table-column>
        <el-table-column prop="maxRobotNum" label="最大下注机器人数" width="130"></el-table-column>
        <el-table-column prop="robotBetTime" label="机器人开始下注秒数" width="130"></el-table-column>
        <el-table-column prop="robotBetInterval" label="机器人下注间隔" width="130"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"  width="150"></el-table-column>
        <el-table-column fixed="right" label="操作" width="360">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="roomFn(scope.row.nnRoomId)" size="mini">房间玩家</el-button>
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">修改牛牛房间</el-button>
            <el-button v-if="scope.row.roomStatus===1" type="primary" @click.native.prevent="freeze(scope.row, 1)" size="mini">禁用</el-button>
            <el-button v-else type="primary" @click.native.prevent="freeze(scope.row, 2)" size="mini">解禁</el-button>
            <el-button type="primary" @click.native.prevent="freeze(scope.row, 3)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '修改牛牛房间': '新增牛牛房间'" :visible.sync="dialogFormVisible" width="450px">
      <el-form :model="form" :rules="rules" label-width="110px" size="mini">
        <el-form-item label="房间名称">
          <el-input style="width:200px" v-model="form.roomName" placeholder="请输入房间名称" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="房间logo">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="下注最小金额" prop="minPrice">
          <el-input style="width:200px" type="number" v-model="form.minPrice" placeholder="请输入下注最小金额" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="下注最大金额" prop="maxPrice">
          <el-input style="width:200px" type="number" v-model="form.maxPrice" placeholder="请输入下注最大金额" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="最大下注人数" prop="maxPeopleNum">
          <el-input style="width:200px" type="number" v-model="form.maxPeopleNum" placeholder="请输入最大下注人数" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="最大下注机器人数" prop="maxRobotNum">
          <el-input style="width:200px" type="number" v-model="form.maxRobotNum" placeholder="请输入最大下注机器人数" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="机器人开始下注秒数" prop="robotBetTime">
          <el-input style="width:200px" type="number" v-model="form.robotBetTime" placeholder="请输入机器人开始下注秒数" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="机器人下注间隔" prop="robotBetInterval">
          <el-input style="width:200px" type="number" v-model="form.robotBetInterval" placeholder="请输入机器人下注间隔" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="房间公告">
          <el-input type="textarea" style="width:200px" v-model="form.roomNotice" placeholder="请输入房间公告" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="机器人状态">
          <el-switch v-model="form.robotStatus" active-color="#13ce66" active-value="1" inactive-value="2" inactive-color="#ff4949" active-text="开启" inactive-text="禁用"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="房间玩家" :visible.sync="dialogFormVisible2" center width="700px">
      <div class="search-zu">
        <div class="search-item four">
          <span class="title">玩家昵称：</span>
          <el-input v-model="requestData2.userName" clearable></el-input>
        </div>
        <div class="search-item three">
          <span class="title">邀请人：</span>
          <el-input v-model="requestData2.invitationUserName" clearable></el-input>
        </div>
        <el-button size="mini" type="primary" icon="el-icon-search" @click="search2">搜索</el-button>
        <el-button-group class="operation-button">
          <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh2">刷新</el-button>
          <el-button size="mini" type="primary" icon="el-icon-plus" @click="roomAdd">添加</el-button>
          <el-button size="mini" type="primary" icon="el-icon-delete" @click="del">删除</el-button>
        </el-button-group>
      </div>
      <div class="my-table center">
        <el-table :data="slUserRoomList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange2">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column prop="userId" label="玩家ID" width="80"></el-table-column>
          <el-table-column prop="userName" label="玩家昵称"></el-table-column>
          <el-table-column prop="invitationUserName" label="邀请人名称"></el-table-column>
          <el-table-column prop="createDate" label="加入时间"></el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange2"
          @current-change="handleCurrentChange2"
          :current-page="requestData2.pageNum"
          :page-sizes="[20, 30, 40, 50]"
          :page-size="requestData2.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count2">
        </el-pagination>
      </div>
    </el-dialog>
    <el-dialog :close-on-click-modal="false" title="新增玩家" :visible.sync="dialogFormVisible3" center width="600px">
      <div class="search-zu">
        <div class="search-item four">
          <span class="title">玩家昵称：</span>
          <el-input v-model="requestData3.userName" clearable></el-input>
        </div>
        <el-button size="mini" type="primary" icon="el-icon-search" @click="search3">搜索</el-button>
        <el-button-group class="operation-button">
          <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh3">刷新</el-button>
          <el-button size="mini" type="primary" icon="el-icon-plus" @click="addUser">新增</el-button>
        </el-button-group>
      </div>
      <div class="my-table center">
        <el-table :data="userList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column prop="userId" label="玩家Id" width="80"></el-table-column>
          <el-table-column prop="userName" label="玩家昵称"></el-table-column>
          <el-table-column prop="phone" label="玩家手机"></el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange3"
          @current-change="handleCurrentChange3"
          :current-page="requestData3.pageNum"
          :page-sizes="[20, 30, 40, 50]"
          :page-size="requestData3.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count3">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameNnRoomList, gameNnUpdateStatus, gameNnRoomEdit, gameNnRoomAdd, gameNnUserRoomList, gameNnUserRoomNotInList, gameNnUserRoomJoinRoom, gameNnUserRoomUpdateStatus} from '@/api'
import MyUpload from '@/components/Upload'
import {baseUrl, checkInt, checkPic} from '@/utils/commom'
export default {
  components: {
    MyUpload
  },
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, roomName: '', status: '', robotStatus: ''},
      requestData2: {pageNum: 1, pageSize: 20, roomName: '', status: '', invitationUserName: '', userName: '', nnRoomId: ''},
      requestData3: {pageNum: 1, pageSize: 20, nnRoomId: '', userName: ''},
      ConfigList: [],
      slUserRoomList: [],
      userList: [],
      count: 0,
      count2: 0,
      count3: 0,
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      form: {roomName: '', roomLogo: '', roomNotice: '', minPrice: '', maxPrice: '', maxPeopleNum: '', maxRobotNum: '', robotBetTime: '', robotBetInterval: '', robotStatus: '1'},
      isedit: false,
      options: [{value: 1, label: '开启'}, {value: 2, label: '禁用'}],
      options1: [{value: 1, label: '开启'}, {value: 2, label: '禁用'}],
      options2: [{value: 1, label: '申请中'}, {value: 2, label: '正常'}, {value: 3, label: '审核驳回'}, {value: 4, label: '退出'}, {value: 5, label: '禁用'}, {value: 6, label: '后台添加'}],
      baseUrl: '',
      option: {
        type: 1,
        file: [],
        count: 1
      },
      rules: {
        minPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        maxPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        bagNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      },
      nnRoomId: '',
      choseArr: [],
      choseArr2: [],
      userIds: [],
      id: []
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.requestData.pageNum = val
      this._gameNnRoomList()
    },
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameNnRoomList()
    },
    handleCurrentChange2 (val) {
      this.requestData2.pageNum = val
      this._gameNnUserRoomList()
    },
    handleSizeChange2 (val) { // 每页条数改变
      this.requestData2.pageSize = val
      this._gameNnUserRoomList()
    },
    handleCurrentChange3 (val) {
      this.requestData3.pageNum = val
      this._gameNnUserRoomNotInList()
    },
    handleSizeChange3 (val) { // 每页条数改变
      this.requestData3.pageSize = val
      this._gameNnUserRoomNotInList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    handleSelectionChange2 (val) {
      this.choseArr2 = val
    },
    search () {
      this.requestData.pageNum = 1
      this._gameNnRoomList()
    },
    search2 () {
      this.requestData2.pageNum = 1
      this._gameNnUserRoomList()
    },
    search3 () {
      this.requestData3.pageNum = 1
      this._gameNnUserRoomNotInList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, roomName: '', status: '', robotStatus: ''}
      this._gameNnRoomList()
    },
    refresh2 () {
      this.requestData2 = {pageNum: 1, pageSize: 20, roomName: '', status: '', invitationUserName: '', userName: '', nnRoomId: this.nnRoomId}
      this._gameNnUserRoomList()
    },
    refresh3 () {
      this.requestData3 = {pageNum: 1, pageSize: 20, nnRoomId: this.nnRoomId, userName: ''}
      this._gameNnUserRoomNotInList()
    },
    upfiles (file) {
      this.form.roomLogo = file.files[0].file
    },
    updelete (file) {
      this.form.roomLogo = ''
    },
    async _gameNnRoomList () {
      const res = await gameNnRoomList(this.requestData)
      if (res.result === '0') {
        this.ConfigList = res.datas.list
        this.count = res.datas.totalRow
      }
    },
    del () {
      if (this.choseArr2.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一名玩家'})
      } else {
        this.id = this.choseArr2.map((item) => {
          return item.id
        })
        this.$confirm('该操作批量删除玩家，是否继续？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const res = await gameNnUserRoomUpdateStatus({status: 7, nnRoomId: this.id.join(',')})
          if (res.result === '1') {
            this._gameNnUserRoomList()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    freeze (row, type) {
      let tip
      type === 1 ? tip = '此操作将禁用该房间，是否继续？' : type === 2 ? tip = '此操作将解禁该房间，是否继续？' : tip = '此操作将删除该房间，是否继续？'
      this.$confirm(tip, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._freeze(row, type)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '操作已取消'
        })
      })
    },
    async _freeze (row, type) {
      let status = type === 1 ? 2 : type === 2 ? 1 : 3
      const res = await gameNnUpdateStatus({status: status, nnRoomId: row.nnRoomId})
      if (res.result === '1') {
        this._gameNnRoomList()
      }
    },
    detailsFn (row) {
      this.option.file = []
      this.dialogFormVisible = true
      this.form.nnRoomId = row.nnRoomId
      this.form.roomName = row.roomName
      this.form.roomLogo = row.roomLogo
      this.option.file.push({file: row.roomLogo})
      this.form.roomNotice = row.roomNotice
      this.form.minPrice = row.minPrice
      this.form.maxPrice = row.maxPrice
      this.form.maxPeopleNum = row.maxPeopleNum
      this.form.maxRobotNum = row.maxRobotNum
      this.form.robotBetTime = row.robotBetTime
      this.form.robotBetInterval = row.robotBetInterval
      this.form.robotStatus = String(row.robotStatus)
      this.isedit = true
    },
    addConfig () {
      this.option.file = []
      this.dialogFormVisible = true
      this.form = {roomName: '', roomLogo: '', roomNotice: '', minPrice: '', maxPrice: '', maxPeopleNum: '', maxRobotNum: '', robotBetTime: '', robotBetInterval: '', robotStatus: '1'}
      this.isedit = false
    },
    async submit () {
      if (this.form.roomName === '') {
        this.$message({type: 'warning', message: '请输入房间名称'})
      } else if (this.form.roomLogo === '') {
        this.$message({type: 'warning', message: '请上传房间logo'})
      } else if (this.form.minPrice === '') {
        this.$message({type: 'warning', message: '请输下注最小金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.minPrice)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.form.maxPrice === '') {
        this.$message({type: 'warning', message: '请输入下注最大金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.maxPrice)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.form.maxPeopleNum === '') {
        this.$message({type: 'warning', message: '请输入最大下注人数'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.maxPeopleNum)) {
        this.$message({type: 'warning', message: '请输入正确的人数'})
      } else if (this.form.maxRobotNum === '') {
        this.$message({type: 'warning', message: '请输入最大下注机器人数'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.maxRobotNum)) {
        this.$message({type: 'warning', message: '请输入正确的机器人数'})
      } else if (this.form.robotBetTime === '') {
        this.$message({type: 'warning', message: '请输入机器人开始下注秒数'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.robotBetTime)) {
        this.$message({type: 'warning', message: '请输入正确的机器人下注秒数'})
      } else if (this.form.robotBetInterval === '') {
        this.$message({type: 'warning', message: '请输入机器人下注间隔'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.form.robotBetInterval)) {
        this.$message({type: 'warning', message: '请输入正确的机器人下注间隔'})
      } else if (this.form.roomNotice === '') {
        this.$message({type: 'warning', message: '请输入房间公告'})
      } else {
        this.form.robotStatus = Number(this.form.robotStatus)
        let res
        this.isedit ? res = await gameNnRoomEdit(this.form) : res = await gameNnRoomAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this._gameNnRoomList()
        }
      }
    },
    async _gameNnUserRoomList () {
      this.requestData2.nnRoomId = this.nnRoomId
      const res = await gameNnUserRoomList(this.requestData2)
      if (res.result === '0') {
        this.slUserRoomList = res.datas.list
        this.count2 = res.datas.totalRow
      }
    },
    roomFn (nnRoomId) {
      this.nnRoomId = nnRoomId
      this._gameNnUserRoomList()
      this.dialogFormVisible2 = true
    },
    async _gameNnUserRoomNotInList () {
      this.requestData3.nnRoomId = this.nnRoomId
      const res = await gameNnUserRoomNotInList(this.requestData3)
      if (res.result === '0') {
        this.userList = res.datas.list
        this.count3 = res.datas.totalRow
      }
    },
    roomAdd () {
      this._gameNnUserRoomNotInList()
      this.dialogFormVisible3 = true
    },
    addUser () {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一名玩家'})
      } else {
        this.userIds = this.choseArr.map((item) => {
          return item.userId
        })
        this.$confirm('该操作将批量添加玩家，是否继续？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._gameNnUserRoomJoinRoom()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    async _gameNnUserRoomJoinRoom () {
      const res = await gameNnUserRoomJoinRoom({nnRoomId: this.nnRoomId, userIds: this.userIds.join(',')})
      if (res.result === '1') {
        this.dialogFormVisible3 = false
        this._gameNnUserRoomList()
      }
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._gameNnRoomList()
  }
}
</script>

<style scoped lang="less">
