  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
