<template>
  <div class="upload-item">
    <input v-if="option.type === 1" type="file" ref="uploadInput" @change="_uploadChange" accept="image/*">
    <input v-else type="file" ref="uploadInput" @change="_uploadChange" accept="image/*">
    <div class="upload-box">
      <div class="img active" v-for="(item, index) in option.file" :key="index">
        <img :src="item.file !== '' ? baseUrl + item.file : ''" alt="">
        <div class="icon">
          <i class="el-icon-zoom-in" @click="preview(index)"></i>
          <i class="el-icon-delete" @click="_delete(index)"></i>
        </div>
        <label class="upload-status"><i class="el-icon-check"></i></label>
      </div>
      <div class="upload" @click="_uploadClick" v-if="option.file.length < option.count">
        <i class="el-icon-plus"></i>
      </div>
    </div>
    <div class="preview-picture" v-show="previewShow" @click.stop.prevent="previewShow=false">
      <img :class="{active: previewShow}" :src="src !== '' ? baseUrl + src : ''" alt="">
      <div class="close">
        <i class="el-icon-close"></i>
      </div>
    </div>
  </div>
</template>

<script>
import {uploadImage} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  props: {
    option: Object
  },
  data () {
    return {
      previewShow: false,
      src: '',
      baseUrl: ''
    }
  },
  methods: {
    _uploadClick () {
      this.$refs.uploadInput.value = ''
      this.$refs.uploadInput.click()
    },
    async _uploadChange (e) {
      this.key = new FormData()
      this.key.append('file', e.target.files[0])
      const res = await uploadImage(this.key)
      if (res.result === '1') {
        if (this.option.type === 1) {
          this.option.file = []
          this.option.file.push({file: res.datas.fileUrl})
        } else {
          this.option.file.push({file: res.datas.fileUrl})
        }
        this.$emit('upfiles', {type: this.option.type, files: this.option.file})
      }
    },
    preview (index) {
      this.previewShow = true
      this.src = this.option.file[index].file
    },
    _delete (index) { // 删除
      this.$message({
        type: 'success',
        message: '删除成功'
      })
      this.option.file.splice(index, 1)
      this.$emit('updelete', {index: index, files: this.option.file})
    }
  },
  created () {
    this.baseUrl = baseUrl
  }
}
</script>

<style scoped lang="less">
  .upload-item {
    input{
      display: none;
    }
    .upload-box{
      .img {
        overflow: hidden;
        background-color: #fff;
        border: 1px solid #c0ccda;
        border-radius: 6px;
        box-sizing: border-box;
        width: 80px;
        height: 80px;
        margin-right: 8px;
        display: inline-block;
        position: relative;
        opacity: 0;
        transform: translate3d(0,-50px,0);
        transition: all .3s;
        &.active{
          opacity: 1;
          transform: translate3d(0,0,0);
        }
        img{
          width: 100%;
          height: 100%;
        }
        .icon{
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          text-align: center;
          color: #fff;
          opacity: 0;
          transition: opacity .3s;
          font-size: 20px;
          background-color: rgba(0,0,0,.5);
          display: flex;
          align-items: center;
          justify-content: center;
          &:hover{
            opacity: 1;
            +.upload-status{
              display: none;
            }
          }
          i{
            cursor: pointer;
            vertical-align: middle;
            &:nth-child(1){
              margin-right: 5px;
            }
          }
        }
        .upload-status{
          position: absolute;
          right: -15px;
          top: -6px;
          width: 40px;
          height: 24px;
          background: #13ce66;
          text-align: center;
          transform: rotate(45deg);
          box-shadow: 0 0 1px 1px rgba(0,0,0,.2);
          display: block;
          i{
            font-size: 12px;
            margin-top: 11px;
            transform: rotate(-45deg);
            color: #fff;
          }
        }
      }
      .upload{
        display: inline-block;
        background-color: #fbfdff;
        border: 1px dashed #c0ccda;
        border-radius: 6px;
        box-sizing: border-box;
        width: 80px;
        height: 80px;
        cursor: pointer;
        line-height: 80px;
        text-align: center;
        vertical-align: top;
        &:hover{
          border-color: #409eff;
          i{
            color: #409eff;
          }
        }
        i{
          font-size: 28px;
          color: #8c939d;
        }
      }
    }
    .preview-picture{
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0,0,0,.5);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 100;
      img{
        // width: 300px;
        opacity: 0;
        transform: translate3d(0,50px,0);
        transition: all 0.3s;
        &.active{
          opacity: 1;
          transform: translate3d(0,0,0);
        }
      }
      .close {
        position: absolute;
        right: 10px;
        top: 10px;
        font-size: 30px;
        color: rgba(194,12,12,.7);
        cursor: pointer;
        &:hover{
          color: rgba(194,12,12,.9);
          background-color: #fff;
        }
      }
    }
  }
</style>



