<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <el-tabs type="border-card" style="height: calc(100% - 40px);" @tab-click="handleClick" v-model="type">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userName" label="昵称"></el-table-column>
            <el-table-column prop="orderNo" label="订单号"></el-table-column>
            <el-table-column prop="payType" label="充值类型">
              <template slot-scope="scope">
                <span v-if="scope.row.payType===1">支付宝</span>
                <span v-if="scope.row.payType===2">微信</span>
              </template>
            </el-table-column>
            <el-table-column prop="rechargePrice" label="充值金额"></el-table-column>
            <el-table-column prop="prPrice" label="实付金额"></el-table-column>
            <el-table-column prop="status" label="状态">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1">充值中</span>
                <span v-if="scope.row.status===2">充值成功</span>
                <span v-if="scope.row.status===3">充值失败</span>
                <span v-if="scope.row.status===3">充值实付金额失败</span>
              </template>
            </el-table-column>
            <el-table-column prop="createDate" label="充值时间"></el-table-column>
            <el-table-column prop="channelType" label="支付渠道">
              <template slot-scope="scope">
                <span v-if="scope.row.channelType===1">东川支付</span>
                <span v-if="scope.row.channelType===2">BUF支付</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
    </el-tabs>

  </div>
</template>

<script>
import {bufRechargeList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: ''},
      options: [{value: 1, label: '充值'}, {value: 2, label: '提现申请'}, {value: 3, label: '提现失败'}, {value: 4, label: '转账支出'}, {value: 5, label: '转账收入'}, {value: 4, label: '商品兑换'}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      dialogFormVisible2: false,
      checkRemark: '',
      id: '',
      type: '1'
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._userRechargeList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._userRechargeList()
    },
    handleClick (val) {
      this.type = val.name
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._userRechargeList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userRechargeList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: ''}
      this._userRechargeList()
    },
    async _userRechargeList () {
      const res = await bufRechargeList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    }
  },
  created () {
    this._userRechargeList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>
<style>
.el-tabs--border-card>.el-tabs__content{
  height: calc(100% - 50px);
}
.el-tab-pane{
  height: 100%;
}
</style>



