<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item search-id-input">
        <span class="title">玩家ID ：</span>
        <el-input v-model="requestData.userId" clearable></el-input>
      </div>
      <div class="search-item search-phone-input">
        <span class="title">玩家手机：</span>
        <el-input v-model="requestData.phone" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="BankCardList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userId" label="玩家ID"></el-table-column>
        <el-table-column prop="phone" label="玩家手机"></el-table-column>
        <el-table-column prop="userName" label="玩家昵称" width="100"></el-table-column>
        <el-table-column prop="trueName" label="玩家姓名" width="90"></el-table-column>
        <el-table-column prop="bankName" label="银行信息" width="150">
          <template slot-scope="scope">
            <img style="display: inline-block;width: 25px;vertical-align: middle;" :src="baseUrl + scope.row.bankLogo" alt="">
            {{scope.row.bankName}}
          </template>
        </el-table-column>
        <el-table-column prop="bankCardNo" label="银行卡号" min-width="200"></el-table-column>
        <el-table-column prop="cardHolder" label="持卡人" width="100"></el-table-column>
        <el-table-column prop="cardTypeName" label="卡类型" width="100"></el-table-column>
        <el-table-column prop="openingBank" label="开户行" width="200"></el-table-column>
        <el-table-column prop="bankCode" label="银行简码" width="100"></el-table-column>
        <el-table-column prop="createDate" label="绑定时间" width="200"></el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>玩家昵称</label>{{dialogData.userName}}</p>
        <p><label>玩家姓名</label>{{dialogData.trueName}}</p>
        <p><label>银行信息</label><img style="width: 25px;vertical-align: middle;" :src="baseUrl + dialogData.bankLogo" @click="showImg(baseUrl + dialogData.bankLogo)" alt="">{{dialogData.bankName}}</p>
        <p><label>银行卡号</label>{{dialogData.bankCardNo}}</p>
        <p><label>卡类型名称</label>{{dialogData.cardTypeName}}</p>
        <p><label>开户行</label>{{dialogData.openingBank}}</p>
        <p><label>银行简称</label>{{dialogData.bankCode}}</p>
        <p><label>绑定时间</label>{{dialogData.createDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {userBankCardList} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', userId: '', phone: ''},
      BankCardList: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {},
      baseUrl: ''
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._userBankCardList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._userBankCardList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userBankCardList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20}
      this._userBankCardList()
    },
    async _userBankCardList () {
      const res = await userBankCardList(this.requestData)
      if (res.result === '0') {
        this.BankCardList = res.datas.bankCardList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    },
    showImg (img) {
      this.$store.commit('SHOW_IMG', {src: img, previewShow: true})
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._userBankCardList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



