<!-- 牛牛机器人 -->
<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item five">
        <span class="title">机器人名称：</span>
        <el-input v-model="requestData.robotName" clearable></el-input>
      </div>
      <div class="search-item five">
        <span class="title">机器人状态：</span>
        <el-select clearable v-model="requestData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options1" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addConfig">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">冻结</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">恢复正常</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="ConfigList" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="robotName" label="机器人名称"></el-table-column>
        <el-table-column prop="robotLogo" label="机器人LOGO" width="110">
          <template slot-scope="scope">
            <img style="width:90px" :src="baseUrl + scope.row.robotLogo" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="mixBetPrice" label="最小下注金额"></el-table-column>
        <el-table-column prop="maxBetPrice" label="最大下注金额"></el-table-column>
        <el-table-column prop="status" label="机器人状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">开启</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建人"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="modifySysUserName" label="修改人"></el-table-column>
        <el-table-column prop="modifyDate" label="修改时间"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="140">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">修改机器人信息</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '修改机器人': '新增机器人'" :visible.sync="dialogFormVisible" width="450px">
      <el-form :model="form" label-width="110px" size="mini">
        <el-form-item label="机器人名称">
          <el-input style="width:200px" v-model="form.robotName" placeholder="请输入机器人名称" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="机器人logo">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="最小下注金额">
          <el-input style="width:200px" type="number" v-model="form.mixBetPrice" placeholder="请输入最小下注金额" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="最大下注金额">
          <el-input style="width:200px" type="number" v-model="form.maxBetPrice" placeholder="请输入最大下注金额" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameNnRobotList, gameNnRobotUpdateStatus, gameNnRobotEdit, gameNnRobotAdd} from '@/api'
import MyUpload from '@/components/Upload'
import {baseUrl} from '@/utils/commom'
export default {
  components: {
    MyUpload
  },
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, robotName: '', status: ''},
      ConfigList: [],
      dialogFormVisible: false,
      form: {nnRobotId: '', robotName: '', logo: '', mixBetPrice: '', maxBetPrice: ''},
      count: 0,
      isedit: false,
      options1: [{value: 1, label: '开启'}, {value: 2, label: '禁用'}],
      baseUrl: '',
      option: {
        type: 1,
        file: [],
        count: 1
      },
      userIds: [],
      choseArr: []
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.requestData.pageNum = val
      this._gameRobotList()
    },
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameRobotList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameRobotList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, robotName: '', status: ''}
      this._gameRobotList()
    },
    upfiles (file) {
      this.form.logo = file.files[0].file
    },
    updelete (file) {
      this.form.logo = ''
    },
    async _gameRobotList () {
      const res = await gameNnRobotList(this.requestData)
      if (res.result === '0') {
        this.ConfigList = res.datas.robotList
        this.count = res.datas.totalRow
      }
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    freeze (row) {
      if (this.choseArr.length === 0) {
        this.$message({type: 'warning', message: '请至少选择一个机器人'})
      } else {
        this.userIds = this.choseArr.map((item) => {
          return item.nnRobotId
        })
        let tip
        row === 1 ? tip = '此操作将批量解冻选择的机器人，是否继续？' : row === 2 ? tip = '此操作将批量冻结选择的机器人，是否继续？' : tip = '此操作将批量删除选择的机器人，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    async _freeze (row) {
      const res = await gameNnRobotUpdateStatus({status: row, ids: this.userIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this._gameRobotList()
      }
    },
    detailsFn (row) {
      this.option.file = []
      this.dialogFormVisible = true
      this.form.nnRobotId = row.nnRobotId
      this.form.robotName = row.robotName
      this.form.logo = row.robotLogo
      this.option.file.push({file: row.robotLogo})
      this.form.mixBetPrice = row.mixBetPrice
      this.form.maxBetPrice = row.maxBetPrice
      this.isedit = true
    },
    addConfig () {
      this.dialogFormVisible = true
      this.option.file = []
      this.form = {robotName: '', logo: '', mixBetPrice: '', maxBetPrice: ''}
      this.isedit = false
    },
    async submit () {
      if (this.form.robotName === '') {
        this.$message({type: 'warning', message: '请输入机器人名称'})
      } else if (this.form.logo === '') {
        this.$message({type: 'warning', message: '请上传机器人logo'})
      } else if (this.form.mixBetPrice === '') {
        this.$message({type: 'warning', message: '请输入最小下注金额'})
      } else if (this.form.maxBetPrice === '') {
        this.$message({type: 'warning', message: '请输入最大下注金额'})
      } else {
        let res
        this.isedit ? res = await gameNnRobotEdit(this.form) : res = await gameNnRobotAdd(this.form)
        if (res.result === '1') {
          this.dialogFormVisible = false
          this._gameRobotList()
        }
      }
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._gameRobotList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



