<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">用户名称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">房间名称：</span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="requestData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div><div class="search-item four">
        <span class="title">发包类型：</span>
        <el-select clearable v-model="requestData.sendType" placeholder="请选择">
          <el-option v-for="(item, index) in options1" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="roomName" label="房间名称" width="100"></el-table-column>
        <el-table-column prop="sendType" label="发包类型" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.sendType===1">玩家发包</span>
            <span v-if="scope.row.sendType===2">机器人发包</span>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="玩家名称" width="100"></el-table-column>
        <el-table-column prop="robotName" label="机器人名称" width="100"></el-table-column>
        <el-table-column prop="sendPrice" label="发包金额" width="100"></el-table-column>
        <el-table-column prop="lossValue" label="中雷数字" width="100"></el-table-column>
        <el-table-column prop="bagNum" label="红包数量" width="100"></el-table-column>
        <el-table-column prop="receiveNum" label="领包数量" width="100"></el-table-column>
        <el-table-column prop="openPrice" label="拆包金额" width="100"></el-table-column>
        <el-table-column prop="returnPrice" label="返还金额" width="100"></el-table-column>
        <el-table-column prop="status" label="状态" width="90">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1">待处理</span>
            <span v-if="scope.row.status===2">已发货</span>
            <span v-if="scope.row.status===3">已完成</span>
          </template>
        </el-table-column>
        <el-table-column prop="createDate" label="发包时间" min-width="150"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>房间名称</label>{{dialogData.roomName}}</p>
        <p><label>发包类型</label><span v-if="dialogData.sendType===1">玩家发包</span><span v-if="dialogData.sendType===2">机器人发包</span></p>
        <p><label>玩家名称</label>{{dialogData.userName}}</p>
        <p><label>机器人名称</label>{{dialogData.robotName}}</p>
        <p><label>发包金额</label>{{dialogData.sendPrice}}</p>
        <p><label>中雷数字</label>{{dialogData.lossValue}}</p>
        <p><label>红包数量</label>{{dialogData.bagNum}}</p>
        <p><label>领包数量</label>{{dialogData.receiveNum}}</p>
        <p><label>拆包金额</label>{{dialogData.openPrice}}</p>
        <p><label>返还金额</label>{{dialogData.returnPrice}}</p>
        <p><label>状态</label><span v-if="dialogData.status===1">待处理</span><span v-if="dialogData.status===2">已发货</span><span v-if="dialogData.status===3">已完成</span></p>
        <p><label>发包时间</label>{{dialogData.createDate}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlSendInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', roomName: '', status: '', sendType: ''},
      options: [{value: 1, label: '待处理'}, {value: 2, label: '已发货'}, {value: 3, label: '已完成'}],
      options1: [{value: 1, label: '玩家发包'}, {value: 2, label: '机器人发包'}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameSlSendInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._gameSlSendInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlSendInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', roomName: '', status: '', sendType: ''}
      this._gameSlSendInfoList()
    },
    async _gameSlSendInfoList () {
      const res = await gameSlSendInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.slSendInfoList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameSlSendInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



