<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <el-tabs type="border-card" style="height: calc(100% - 40px);" @tab-click="handleClick" v-model="type">
      <el-tab-pane label="上分" name="1">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userId" label="用户ID"></el-table-column>
            <el-table-column prop="userName" label="昵称"></el-table-column>
            <el-table-column prop="phone" label="手机号码"></el-table-column>
            <el-table-column prop="type" label="类型">
              <template slot-scope="scope">
                <span v-if="scope.row.changeType===1">上分</span>
                <span v-if="scope.row.changeType===2">下分</span>
              </template>
            </el-table-column>
            <el-table-column prop="changeAccountPrice" label="上分数量" ></el-table-column>
            <el-table-column prop="remark" label="备注"></el-table-column>
            <el-table-column prop="createDate" label="操作时间"></el-table-column>
            <el-table-column prop="createSysUserName" label="操作人"></el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="下分" name="2">
        <div class="my-table center">
          <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
            <el-table-column prop="userId" label="用户ID"></el-table-column>
            <el-table-column prop="userName" label="昵称"></el-table-column>
            <el-table-column prop="phone" label="手机号码"></el-table-column>
            <el-table-column prop="type" label="类型">
              <template slot-scope="scope">
                <span v-if="scope.row.changeType===1">上分</span>
                <span v-if="scope.row.changeType===2">下分</span>
              </template>
            </el-table-column>
            <el-table-column prop="changeAccountPrice" label="下分数量"></el-table-column>
            <el-table-column prop="remark" label="备注"></el-table-column>
            <el-table-column prop="createDate" label="操作时间" ></el-table-column>
            <el-table-column prop="createSysUserName" label="操作人"></el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="requestData.pageNum"
            :page-sizes="[20, 30, 40, 50]"
            :page-size="requestData.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import {changeAccountInfoList} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      requestData: {pageNum: 1, pageSize: 20, userName: '', changeType: '1'},
      list: [],
      totalNum: 0,
      type: '1'
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._changeAccountInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._changeAccountInfoList()
    },
    handleClick (val) {
      this.type = val.name
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', changeType: val.name}
      this._changeAccountInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._changeAccountInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', changeType: this.type}
      this._changeAccountInfoList()
    },
    async _changeAccountInfoList () {
      const res = await changeAccountInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._changeAccountInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>
<style>
.el-tabs--border-card>.el-tabs__content{
  height: calc(100% - 50px);
}
.el-tab-pane{
  height: 100%;
}
</style>



