<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">角色描述：</span>
        <el-input v-model="serachData.roleRemark" clearable></el-input>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addRole">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit">编辑</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="del">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="ConMenu(scope.row.sysRoleId)">配置系统菜单</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="roleName" label="角色名称" width="150"></el-table-column>
        <el-table-column prop="roleRemark" label="角色描述"></el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑角色': '新增角色'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="80px" size="mini">
        <el-form-item label="角色名称">
          <el-input style="width:180px" v-model="form.roleName" placeholder="请输入角色名称" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色备注">
          <el-input type="textarea" style="width:180px" v-model="form.roleRemark" placeholder="请输入角色备注" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="配置系统菜单" :visible.sync="dialogFormVisible2" width="400px">
      <div class="plan-con">
        <div class="button-box">
          <el-button size="mini" type="primary" @click="getCheckedNodes">授权选中菜单</el-button>
        </div>
        <div class="tree">
          <el-tree
            :data="data2"
            show-checkbox
            default-expand-all
            node-key="id"
            ref="tree"
            highlight-current
            :default-checked-keys="checked"
            :props="defaultProps">
          </el-tree>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {sysRoleList, sysRoleAdd, sysRoleEdit, sysRoleDelete, roleMenuList, addRoleMenu} from '@/api'
export default {
  data () {
    return {
      dialogFormVisible: false,
      dialogFormVisible2: false,
      isedit: false,
      form: {roleName: '', roleRemark: ''},
      choseArr: [],
      totalNum: 1,
      serachData: {roleRemark: '', pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      defaultProps: {
        children: 'menuTwoList',
        label: 'menuName'
      },
      checkedId: '',
      checked: []
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._sysRoleList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._sysRoleList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    async _sysRoleList () {
      const res = await sysRoleList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.sysRoleList
      }
    },
    async submit () {
      if (this.form.roleName === '') {
        this.$message({type: 'warning', message: '请输入角色名称'})
      } else if (this.form.roleRemark === '') {
        this.$message({type: 'warning', message: '请输入角色备注'})
      } else if (!this.isedit) {
        const res = await sysRoleAdd(this.form)
        if (res.result === '1') {
          this._sysRoleList()
          this.dialogFormVisible = false
          this.setReset()
        }
      } else {
        const res = await sysRoleEdit({sysRoleId: this.form.sysRoleId, roleName: this.form.roleName, roleRemark: this.form.roleRemark})
        if (res.result === '1') {
          this._sysRoleList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addRole () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    edit () {
      if (this.choseArr.length !== 1) {
        this.$message({type: 'warning', message: '请选择一个角色进行编辑'})
      } else {
        this.dialogFormVisible = true
        this.form.roleName = this.choseArr[0].roleName
        this.form.roleRemark = this.choseArr[0].roleRemark
        this.form.sysRoleId = this.choseArr[0].sysRoleId
        this.isedit = true
      }
    },
    del () {
      if (this.choseArr.length < 1) {
        this.$message({type: 'warning', message: '请至少选择一个角色进行删除'})
      } else {
        let ids = []
        this.choseArr.forEach(element => {
          ids.push(element.sysRoleId)
        })
        this.$confirm('此操作将批量删除选择的角色，是否继续？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          await sysRoleDelete({sysRoleIds: ids.join(',')})
          this._sysRoleList()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {roleName: '', roleRemark: ''}
    },
    refresh () {
      this.serachData = {roleRemark: '', pageNum: 1, pageSize: 20}
      this._sysRoleList()
    },
    search () {
      this.serachData.pageNum = 1
      this._sysRoleList()
    },
    async ConMenu (id) {
      this.checkedId = id
      this.checked = []
      const res = await roleMenuList({roleId: this.checkedId})
      res.datas.menuList.map(item => {
        if (item.type === 'true') {
          this.checked.push(item.id)
        }
        item.menuTwoList.map(item2 => {
          if (item2.type === 'true') {
            this.checked.push(item2.id)
          }
        })
      })
      this.data2 = res.datas.menuList
      this.dialogFormVisible2 = true
    },
    async getCheckedNodes () {
      if (this.$refs.tree.getCheckedNodes().length > 0) {
        let menuIds = []
        this.$refs.tree.getCheckedNodes().map(item => {
          menuIds.push(item.id)
        })
        const res = await addRoleMenu({roleId: this.checkedId, menuId: menuIds.join(',')})
        if (res.result === '1') {
          this.dialogFormVisible2 = false
        }
      } else {
        this.$message({message: '菜单ID不能为空', type: 'warning'})
      }
    }
  },
  created () {
    this._sysRoleList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
}
</style>



