<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title">状态：</span>
        <el-select clearable v-model="serachData.status" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
        <el-button size="mini" type="primary" icon="el-icon-plus" @click="addRole">新增</el-button>
        <el-button size="mini" type="primary" icon="el-icon-goods" @click="freeze(2)">禁用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-check" @click="freeze(1)">启用</el-button>
        <el-button size="mini" type="primary" icon="el-icon-delete" @click="freeze(3)">删除</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="advertId" label="轮播图Id"></el-table-column>
        <el-table-column prop="sort" label="序号"></el-table-column>
        <el-table-column prop="advertPic" label="轮播图" width="130">
          <template slot-scope="scope">
            <img style="width:110px" :src="baseUrl + scope.row.advertPic" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="createSysUserName" label="创建者"></el-table-column>
        <el-table-column prop="createDate" label="创建时间"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 1" style="color:#67c23a">正常</span>
            <span v-if="scope.row.status === 2" style="color:#eb453c">禁用</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="edit(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog :title="isedit ? '编辑轮播图': '新增轮播图'" :visible.sync="dialogFormVisible" width="350px">
      <el-form :model="form" label-width="100px" size="mini">
        <el-form-item label="轮播图标题">
          <el-input style="width:180px" v-model="form.advertTitle" placeholder="请输入轮播图标题" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="轮播图">
          <my-upload :option="option" @updelete="updelete" @upfiles="upfiles"/>
        </el-form-item>
        <el-form-item label="序号">
          <el-input style="width:180px" v-model="form.sort" placeholder="请输入序号" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {advertList, advertAdd, advertInfo, advertEdit, advertEditStatus} from '@/api'
import MyUpload from '@/components/Upload'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      dialogFormVisible: false,
      isedit: false,
      form: {advertPic: '', sort: '', advertTitle: ''},
      choseArr: [],
      totalNum: 0,
      serachData: {status: '', pageNum: 1, pageSize: 20},
      tableData: [],
      data2: [],
      checkedId: '',
      checked: [],
      option: {
        type: 1,
        file: [],
        count: 1
      },
      options: [{value: 1, label: '正常'}, {value: 2, label: '禁用'}],
      advertIds: []
    }
  },
  components: {
    MyUpload
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._advertList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._advertList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    upfiles (file) {
      this.form.advertPic = file.files[0].file
    },
    updelete (file) {
      this.form.advertPic = ''
    },
    async _advertList () {
      const res = await advertList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.advertList
      }
    },
    async submit () {
      if (this.form.advertTitle === '') {
        this.$message({type: 'warning', message: '请输入轮播图标题'})
      } else if (this.form.advertPic === '') {
        this.$message({type: 'warning', message: '请上传轮播图'})
      } else if (this.form.sort === '') {
        this.$message({type: 'warning', message: '请输入展示序号'})
      } else if (!this.isedit) {
        const res = await advertAdd(this.form)
        if (res.result === '1') {
          this._advertList()
          this.dialogFormVisible = false
          this.setReset()
        }
      } else {
        const res = await advertEdit(this.form)
        if (res.result === '1') {
          this._advertList()
          this.$refs.table.clearSelection()
          this.choseArr = []
          this.dialogFormVisible = false
          this.setReset()
        }
      }
    },
    addRole () {
      this.dialogFormVisible = true
      this.isedit = false
      this.setReset()
    },
    async edit (row) {
      this.option.file = []
      this.dialogFormVisible = true
      const res = await advertInfo({advertId: row.advertId})
      this.form.advertId = row.advertId
      this.form.sort = row.sort
      this.form.advertPic = res.datas.advertPic
      this.option.file.push({file: row.advertPic})
      this.form.advertTitle = res.datas.advertTitle
      this.isedit = true
    },
    async _freeze (row) {
      const res = await advertEditStatus({status: row, advertIds: this.advertIds.join(',')})
      if (res.result === '1') {
        this.$refs.table.clearSelection()
        this.choseArr = []
        this.advertIds = []
        this._advertList()
      }
    },
    freeze (row) {
      if (this.choseArr.length < 1) {
        this.$message({type: 'warning', message: '请至少选择一张轮播图'})
      } else {
        this.choseArr.forEach(element => {
          this.advertIds.push(element.advertId)
        })
        let tip
        row === 1 ? tip = '此操作将批量启用轮播图，是否继续？' : row === 2 ? tip = '此操作将批量禁用轮播图，是否继续？' : tip = '此操作将批量删除轮播图，是否继续？'
        this.$confirm(tip, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._freeze(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '操作已取消'
          })
        })
      }
    },
    cancle () {
      this.dialogFormVisible = false
      this.setReset()
    },
    setReset () {
      this.form = {advertPic: '', sort: '', advertTitle: ''}
      this.option.file = []
    },
    refresh () {
      this.serachData = {status: '', pageNum: 1, pageSize: 20}
      this._advertList()
    },
    search () {
      this.serachData.pageNum = 1
      this._advertList()
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._advertList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
}
.editor-container {
  max-height: 500px;
  margin: 0 0 30px;
  .editor-upload-btn-container {
    text-align: right;
    margin-right: 10px;
    .editor-upload-btn {
      display: inline-block;
    }
  }
}
</style>



