<template>
  <div class="con">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="mini">
      <el-form-item label="手续费" prop="poundage">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.poundage"></el-input> %
      </el-form-item>
      <el-form-item label="提现金额倍数" prop="multiples">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.multiples"></el-input>
      </el-form-item>
      <el-form-item label="最小提现金额" prop="minPrice">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.minPrice"></el-input>
      </el-form-item>
      <el-form-item label="当日提现限额" prop="restrictionPrice">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.restrictionPrice"></el-input>
      </el-form-item>
      <el-form-item label="当日提现次数" prop="restrictionNum">
        <el-input type="number" :min="0" style="width:190px;" v-model="ruleForm.restrictionNum"></el-input>
      </el-form-item>
      <el-form-item label="提现开始时间">
        <el-time-picker v-model="ruleForm.withdrawBeginTime" value-format="HH:mm:ss" placeholder="选择时间"></el-time-picker>
      </el-form-item>
      <el-form-item label="提现结束时间">
        <el-time-picker v-model="ruleForm.withdrawEndTime" value-format="HH:mm:ss" placeholder="选择时间"></el-time-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {withdrawConfigureList, withdrawConfigureEdit} from '@/api'
import {checkFourFlaseSmallNumber, checkPic, checkInt} from '@/utils/commom'
export default {
  data () {
    return {
      ruleForm: {
        poundage: '',
        multiples: '',
        minPrice: '',
        withdrawBeginTime: '',
        withdrawEndTime: '',
        restrictionPrice: '',
        restrictionNum: ''
      },
      rules: {
        poundage: [
          { validator: checkFourFlaseSmallNumber, trigger: 'change' }
        ],
        minPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        restrictionPrice: [
          { validator: checkPic, trigger: 'change' }
        ],
        restrictionNum: [
          { validator: checkInt, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    async _withdrawConfigureList () {
      const res = await withdrawConfigureList({})
      if (res.result === '0') {
        this.ruleForm = res.datas
      }
    },
    async submitForm () {
      if (this.ruleForm.poundage === '') {
        this.$message({type: 'warning', message: '请输入手续费'})
      } else if (!/^(([0-9]+|0)\.([0-9]{4}))$/.test(this.ruleForm.poundage)) {
        this.$message({type: 'warning', message: '手续费请输入4位非负小数'})
      } else if (this.ruleForm.multiples === '') {
        this.$message({type: 'warning', message: '请输入提现金额倍数'})
      } else if (this.ruleForm.minPrice === '') {
        this.$message({type: 'warning', message: '请输入最小提现金额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.ruleForm.minPrice)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.ruleForm.restrictionPrice === '') {
        this.$message({type: 'warning', message: '请输入当日提现限额'})
      } else if (!/(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/.test(this.ruleForm.restrictionPrice)) {
        this.$message({type: 'warning', message: '请输入正确的金额'})
      } else if (this.ruleForm.restrictionNum === '') {
        this.$message({type: 'warning', message: '请输入当日提现次数'})
      } else if (!/^[1-9]*[1-9][0-9]*$/.test(this.ruleForm.restrictionNum)) {
        this.$message({type: 'warning', message: '当日提现次数请输入正整数'})
      } else if (this.ruleForm.withdrawBeginTime === '') {
        this.$message({type: 'warning', message: '请选择提现开始时间'})
      } else if (this.ruleForm.withdrawEndTime === '') {
        this.$message({type: 'warning', message: '请选择提现结束时间'})
      } else {
        const res = await withdrawConfigureEdit(this.ruleForm)
        if (res.result === '1') {
        }
      }
    }
  },
  created () {
    this._withdrawConfigureList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
}
</style>



