<template>
  <div class="head">
    <span class="menu-drag" @click="open" >
      <i v-if="!isCollapse" class="icon iconfont icon-shouqicaidan" title="收起"></i>
      <i v-else class="icon iconfont icon-zhankaicaidan" title="展开"></i>
    </span>
    <div class="nav">
      <!-- <i class="icon iconfont icon-weizhi"></i> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!-- <el-breadcrumb-item>首页</el-breadcrumb-item> -->
        <el-breadcrumb-item v-if="$route.matched[0].meta.title">{{$route.matched[0].meta.title}}</el-breadcrumb-item>
        <el-breadcrumb-item v-if="$route.matched[0].meta.title && $route.matched[1].meta.titel">{{$route.matched[1].meta.titel}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="msg">
      <el-badge :value="pendingRechargeNum" :max="99" class="item">
        <el-button size="mini" @click="goRechargeList">上分充值</el-button>
      </el-badge>
      <el-badge :value="pendingWithdrawNum" :max="99" class="item">
        <el-button size="mini" @click="goWithdrawList">下分提现</el-button>
      </el-badge>
    </div>
    <div class="dropdown">
      <el-dropdown @command="handleCommand">
        <span class="el-dropdown-link">
          {{userInfo.userName}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="1">修改密码</el-dropdown-item>
          <el-dropdown-item command="2" divided>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <audio :src='rechargeApplyMp3' ref="rechargeApplyMp3"></audio>
    <audio :src='withdrawApplyMp3' ref="withdrawApplyMp3"></audio>
  </div>
</template>
<script>
import {loginOut} from '@/api'
import {removeToken, getToken} from '@/utils/auth'
import {wsUrl} from '@/utils/commom'
export default {
  props: {
    isCollapse: {
      type: Boolean,
      default: false
    },
    username: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showBtn: false,
      rechargeApplyMp3: require('../../../../assets/mp3/recharge_apply.mp3'),
      withdrawApplyMp3: require('../../../../assets/mp3/withdraw_apply.mp3'),
      pendingRechargeNum: 0, // 上分请求数量
      pendingWithdrawNum: 0, // 下分请求数量
      userInfo: {
        userName: ''
      }
    }
  },
  methods: {
    open () {
      this.isCollapse ? this.$emit('open', false) : this.$emit('open', true)
    },
    handleCommand (command) {
      if (command === '1') {
        this.$router.push('/PasswordEdit')
      } else if (command === '2') {
        this._loginOut()
      }
    },
    async _loginOut () { // 退出登录
      await loginOut()
      removeToken()
      this.$router.push('/Login')
    },
    goRechargeList () { // 跳转
      this.$router.push('RechargeList')
    },
    goWithdrawList () { // 跳转
      this.$router.push('WithdrawList')
    },
    initWebSocket () { // 初始化weosocket
      let token = getToken() // 2ea8a72b37f1b89fe95a19325f1bf072
      const wsUri = 'ws://' + window.location.host + wsUrl + token // ws地址
      this.websock = new WebSocket(wsUri)
      this.websock.onmessage = this.websocketonmessage
      this.websock.onclose = this.websocketclose
    },
    websocketonmessage (e) { // 数据接收
      const respData = JSON.parse(e.data)
      const socketType = respData.socketType
      if (socketType === 1) {
        if (respData.pendingRechargeNum > 0) {
          this.$refs.rechargeApplyMp3.play()
        }
        if (respData.pendingWithdrawNum > 0) {
          setTimeout(() => {
            this.$refs.withdrawApplyMp3.play()
          }, 2000)
        }
      } else if (socketType === 2) {
        this.$refs.rechargeApplyMp3.play()
      } else if (socketType === 3) {
        this.$refs.withdrawApplyMp3.play()
      }

      this.pendingRechargeNum = respData.pendingRechargeNum
      this.pendingWithdrawNum = respData.pendingWithdrawNum
    },
    websocketsend (agentData) { // 数据发送
      this.websock.send(agentData)
    },
    websocketclose (e) { // 关闭
      this.initWebSocket()
      // console.log('connection closed (' + e.code + ')')
    }
  },
  created () {
    this.initWebSocket()
    if (sessionStorage.getItem('userInfo')) {
      this.userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
    }
  }
}
</script>
<style scoped lang="less">
.head{
  height: 49px;
  padding-left: 10px;
  font-size: 0;
  display: flex;
  align-items: center;
  position: relative;
  border-bottom: 1px solid #e2e9ef;

  .msg{
    position: absolute;
    top: 11px;
    right: 200px;
    .item{
      margin-left: 30px;
    }
  }
  .dropdown{
    position: absolute;
    right: 20px;
    line-height: 50px;
    padding: 0 5px;
    top: 0;
    &:hover{
      &::after{
        width: 100%;
      }
    }
    &::after{
      content: '';
      position:absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 0;
      height: 2px;
      margin: 0 auto;
      background-color: #333;
      transition: all 0.2s;
    }
  }
}
.menu-drag{
  line-height: 50px;
  margin: 0 20px;
  display: inline-block;
  cursor: pointer;
  position: relative;
  &:hover{
    &::after{
      width: 100%;
    }
  }
  &::after{
    content: '';
    position:absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 0;
    height: 2px;
    margin: 0 auto;
    background-color: #20222A;
    transition: all 0.2s;
  }
  .icon{
    font-size: 25px;
    color: #333;
    vertical-align: middle;
  }
}
</style>



