<template>
  <div class="con">
    <div class="search-zu">
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="tableData" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini" @selection-change="handleSelectionChange" :row-class-name="tableRowClassName">
        <el-table-column width="130" prop="statisticsDate" fixed label="统计时间"></el-table-column>
        <el-table-column width="130" prop="gatherPrice" fixed label="收益汇总金额"></el-table-column>
        <el-table-column width="130" prop="slGatherPrice" fixed label="扫雷收益汇总金额"></el-table-column>
        <el-table-column width="130" prop="jlgGatherPrice" fixed label="接龙收益汇总金额"></el-table-column>
        <el-table-column width="130" prop="nnGatherPrice" fixed label="牛牛收益汇总金额"></el-table-column>

        <el-table-column width="130" prop="slSysReceivePrice" label="扫雷系统领包金额"></el-table-column>
        <el-table-column width="130" prop="slRobotReceivePrice" label="扫雷机器人领包金额"></el-table-column>
        <el-table-column width="130" prop="slBrokeragePrice" label="扫雷佣金金额"></el-table-column>
        <el-table-column width="130" prop="slRobotSendPrice" label="扫雷机器人发包金额"></el-table-column>
        <el-table-column width="130" prop="slRobotSendRestorePrice" label="扫雷机器人发包返还金额"></el-table-column>
        <el-table-column width="130" prop="slLossAwardPrice" label="扫雷中雷奖励金额"></el-table-column>
        <el-table-column width="130" prop="slReceiveAwardPrice" label="扫雷领包奖励金额"></el-table-column>
        <el-table-column width="130" prop="slAwarePrice" label="扫雷抽奖奖励金额"></el-table-column>
        <el-table-column width="130" prop="slRobotDamageUserPrice" label="扫雷机器人赔付给玩家金额"></el-table-column>
        <el-table-column width="130" prop="slUserDamageRobotPrice" label="玩家赔付给机器人金额"></el-table-column>

        <el-table-column width="130" prop="jlSysReceivePrice" label="接龙系统领包金额"></el-table-column>
        <el-table-column width="130" prop="jlBrokeragePrice" label="接龙佣金金额"></el-table-column>
        <el-table-column width="130" prop="jlSysSendPrice" label="接龙系统发包金额"></el-table-column>
        <el-table-column width="130" prop="jlReceiveAwardPrice" label="接龙领包奖励金额"></el-table-column>

        <el-table-column width="130" prop="nnWaterPrice" label="牛牛抽水金额"></el-table-column>
        <el-table-column width="130" prop="nnRobotReceivePrice" label="牛牛机器人领包金额"></el-table-column>
        <el-table-column width="130" prop="nnBrokeragePrice" label="牛牛佣金金额"></el-table-column>
        <el-table-column width="130" prop="nnRobotBetPrice" label="牛牛机器人下注金额"></el-table-column>
        <el-table-column width="130" prop="nnDamagePrice" label="牛牛赔付机器人金额"></el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="serachData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="serachData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
  </div>
</template>
<style>
  .el-table .green-row {
    background: #f0f9eb;
  }
  .el-table .oldLace-row {
    background: oldlace;
  }

</style>
<script>
import {dayGatherList} from '@/api'
import {baseUrl} from '@/utils/commom'
export default {
  data () {
    return {
      baseUrl: '',
      dialogFormVisible: false,
      choseArr: [],
      totalNum: 0,
      serachData: {pageNum: 1, pageSize: 20},
      tableData: [],
      advertIds: []
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.serachData.pageSize = val
      this._statisticsDayList()
    },
    handleCurrentChange (val) { // page改变
      this.serachData.pageNum = val
      this._statisticsDayList()
    },
    handleSelectionChange (val) {
      this.choseArr = val
    },
    tableRowClassName ({row, rowIndex}) {
      if (rowIndex % 4 === 0) {
        return 'green-row'
      }
      if (rowIndex % 4 === 2) {
        return 'oldLace-row'
      }
      return ''
    },
    async _statisticsDayList () {
      const res = await dayGatherList(this.serachData)
      if (res.result === '0') {
        this.totalNum = res.datas.totalRow
        this.tableData = res.datas.statisticsList
      }
    },
    setReset () {
      this.form = {advertPic: '', sort: '', advertTitle: ''}
      this.option.file = []
    },
    refresh () {
      this.serachData = {status: '', pageNum: 1, pageSize: 20}
      this._statisticsDayList()
    },
    search () {
      this.serachData.pageNum = 1
      this._statisticsDayList()
    }
  },
  created () {
    this.baseUrl = baseUrl
    this._statisticsDayList()
  }
}
</script>
<style scoped lang="less">
.con{
  height: 100%;
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .button-box{
    width: 100%;
    height: 28px;
    button{
      float: right;
    }
  }
  .tree{
    max-height: 400px;
    overflow: auto;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
}
.editor-container {
  max-height: 500px;
  margin: 0 0 30px;
  .editor-upload-btn-container {
    text-align: right;
    margin-right: 10px;
    .editor-upload-btn {
      display: inline-block;
    }
  }
}
</style>



