/* 工具接口为了方便构建全局state自定义方法 */
const getters = {
  currentPage: state => state.route.currentPage,
  loading: state => state.route.loading,
  turnPage: state => state.route.turnPage,
  routerMenu: state => state.route.routerMenu,
  showImg: state => state.route.showImg
}

export default getters



