<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item four">
        <span class="title">玩家名称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item five">
        <span class="title">机器人名称：</span>
        <el-input v-model="requestData.robotName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">房间名称：</span>
        <el-input v-model="requestData.roomName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">领包类型：</span>
        <el-select clearable v-model="requestData.damageType" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label="领包玩家名称"></el-table-column>
        <el-table-column prop="robotName" label="领包机器人名称"></el-table-column>
        <el-table-column prop="sendUserName" label="发包人名称"></el-table-column>
        <el-table-column prop="sendRobotName" label="发包机器人名称"></el-table-column>
        <el-table-column prop="roomName" label="房间名称"></el-table-column>
        <el-table-column prop="damageType" label="赔付类型">
          <template slot-scope="scope">
            <span v-if="scope.row.damageType===1">玩家赔付</span>
            <span v-if="scope.row.damageType===2">机器人赔付</span>
          </template>
        </el-table-column>
        <el-table-column prop="damagePrice" label="赔付金额"></el-table-column>
        <el-table-column prop="damageRadio" label="赔付倍数"></el-table-column>
        <el-table-column prop="validNum" label="玩家当前余额"></el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>领包玩家名称</label>{{dialogData.userName}}</p>
        <p><label>领包机器人名称</label>{{dialogData.robotName}}</p>
        <p><label>发包人名称</label>{{dialogData.sendUserName}}</p>
        <p><label>发包机器人名称</label>{{dialogData.sendRobotName}}</p>
        <p><label>赔付类型</label><span v-if="dialogData.damageType===1">玩家赔付</span><span v-if="dialogData.damageType===2">机器人赔付</span></p>
        <p><label>赔付倍数</label>{{dialogData.damageRadio}}</p>
        <p><label>赔付金额</label>{{dialogData.damagePrice}}</p>
        <p><label>玩家当前余额</label>{{dialogData.validNum}}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {gameSlDamageInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', robotName: '', roomName: '', damageType: ''},
      options: [{value: 1, label: '玩家赔付'}, {value: 2, label: '机器人赔付'}],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._gameSlDamageInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._gameSlDamageInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._gameSlDamageInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', robotName: '', roomName: '', damageType: ''}
      this._gameSlDamageInfoList()
    },
    async _gameSlDamageInfoList () {
      const res = await gameSlDamageInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.damageInfoList
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._gameSlDamageInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  padding-bottom: 10px;
  .btn{
    text-align: center;
    margin-bottom: 10px;
  }
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



