<template>
  <div class="con">
    <div class="search-zu">
      <div class="search-item two">
        <span class="title">昵称：</span>
        <el-input v-model="requestData.userName" clearable></el-input>
      </div>
      <div class="search-item four">
        <span class="title">明细类型：</span>
        <el-select clearable v-model="requestData.infoType" placeholder="请选择">
          <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-button-group class="operation-button">
        <el-button size="mini" type="primary" icon="el-icon-refresh" @click="refresh">刷新</el-button>
      </el-button-group>
    </div>
    <div class="my-table center">
      <el-table :data="list" ref="table" style="width: 100%" height="100%" :stripe="true" border highlight-current-row size="mini">
        <el-table-column prop="userName" label="昵称"></el-table-column>
        <el-table-column prop="infoType" label="明细类型">
          <template slot-scope="scope">
            <span v-if="scope.row.infoType===1">充值</span>
            <span v-if="scope.row.infoType===2">提现申请</span>
            <span v-if="scope.row.infoType===3">提现失败</span>
            <span v-if="scope.row.infoType===4">转账支出</span>
            <span v-if="scope.row.infoType===5">转账收入</span>
            <span v-if="scope.row.infoType===6">商品兑换</span>
            <span v-if="scope.row.infoType===11">扫雷发包</span>
            <span v-if="scope.row.infoType===12">扫雷赔付</span>
            <span v-if="scope.row.infoType===13">扫雷领包</span>
            <span v-if="scope.row.infoType===14">扫雷中雷奖励</span>
            <span v-if="scope.row.infoType===15">扫雷领包奖励</span>
            <span v-if="scope.row.infoType===16">扫雷抽奖奖励</span>
            <span v-if="scope.row.infoType===17">扫雷返还</span>
            <span v-if="scope.row.infoType===18">扫雷佣金</span>
            <span v-if="scope.row.infoType===19">扫雷赔付发包</span>
            <span v-if="scope.row.infoType===21">接龙发包</span>
            <span v-if="scope.row.infoType===22">接龙领包</span>
            <span v-if="scope.row.infoType===23">接龙领包奖励</span>
            <span v-if="scope.row.infoType===24">接龙佣金</span>
            <span v-if="scope.row.infoType===25">领包冻结</span>
            <span v-if="scope.row.infoType===26">领包解冻</span>
            <span v-if="scope.row.infoType===27">牛牛下注</span>
            <span v-if="scope.row.infoType===28">牛牛领包</span>
            <span v-if="scope.row.infoType===29">牛牛赔付</span>
            <span v-if="scope.row.infoType===30">牛牛佣金</span>
            <span v-if="scope.row.infoType===31">牛牛返还</span>
            <span v-if="scope.row.infoType===32">平台上分</span>
            <span v-if="scope.row.infoType===33">平台下分</span>
          </template>
        </el-table-column>
        <el-table-column prop="validNum" label="用户当前余额"></el-table-column>
        <el-table-column prop="priceNum" label="金额数量"></el-table-column>
        <el-table-column prop="createDate" label="转账时间"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1" style="color: green">增加</span>
            <span v-if="scope.row.status===2" style="color: #eb453c">减少</span>
          </template>
        </el-table-column>
        <el-table-column prop="registerDate" fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button type="primary" @click.native.prevent="detailsFn(scope.row)" size="mini">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="requestData.pageNum"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="requestData.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="dialogFormVisible" center width="400px">
      <div class="plan-con">
        <p><label>转账人昵称</label>{{dialogData.userName}}</p>
        <p><label>明细类型</label>
          <span v-if="dialogData.infoType===1">充值</span>
          <span v-if="dialogData.infoType===2">提现申请</span>
          <span v-if="dialogData.infoType===3">提现失败</span>
          <span v-if="dialogData.infoType===4">转账支出</span>
          <span v-if="dialogData.infoType===5">转账收入</span>
          <span v-if="dialogData.infoType===6">商品兑换</span>
          <span v-if="dialogData.infoType===11">扫雷发包</span>
          <span v-if="dialogData.infoType===12">扫雷赔付</span>
          <span v-if="dialogData.infoType===13">扫雷领包</span>
          <span v-if="dialogData.infoType===14">扫雷中雷奖励</span>
          <span v-if="dialogData.infoType===15">扫雷领包奖励</span>
          <span v-if="dialogData.infoType===16">扫雷抽奖奖励</span>
          <span v-if="dialogData.infoType===17">扫雷返还</span>
          <span v-if="dialogData.infoType===18">扫雷佣金</span>
          <span v-if="dialogData.infoType===19">扫雷赔付发包</span>
          <span v-if="dialogData.infoType===21">接龙发包</span>
          <span v-if="dialogData.infoType===22">接龙领包</span>
          <span v-if="dialogData.infoType===23">接龙领包奖励</span>
          <span v-if="dialogData.infoType===24">接龙佣金</span>
          <span v-if="dialogData.infoType===25">领包冻结</span>
          <span v-if="dialogData.infoType===26">领包解冻</span>
          <span v-if="dialogData.infoType===27">牛牛下注</span>
          <span v-if="dialogData.infoType===28">牛牛领包</span>
          <span v-if="dialogData.infoType===29">牛牛赔付</span>
          <span v-if="dialogData.infoType===30">牛牛佣金</span>
          <span v-if="dialogData.infoType===31">牛牛返还</span>
          <span v-if="dialogData.infoType===32">平台上分</span>
          <span v-if="dialogData.infoType===33">平台下分</span>
        </p>
        <p><label>用户当前余额</label>{{dialogData.validNum}}</p>
        <p><label>金额数量</label>{{dialogData.priceNum}}</p>
        <p><label>转账时间</label>{{dialogData.createDate}}</p>
        <p><label>状态</label><span v-if="dialogData.status===1"  style="color: green">增加</span><span v-if="dialogData.status===2" style="color: #eb453c">减少</span></p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {userUserAccountInfoList} from '@/api'
export default {
  data () {
    return {
      requestData: {pageNum: 1, pageSize: 20, userName: '', infoType: ''},
      options: [
        {value: 1, label: '充值'},
        {value: 2, label: '提现申请'},
        {value: 3, label: '提现失败'},
        {value: 4, label: '转账支出'},
        {value: 5, label: '转账收入'},
        {value: 6, label: '商品兑换'},
        {value: 11, label: '扫雷发包'},
        {value: 12, label: '扫雷赔付'},
        {value: 13, label: '扫雷领包'},
        {value: 14, label: '扫雷中雷奖励'},
        {value: 15, label: '扫雷领包奖励'},
        {value: 16, label: '扫雷抽奖奖励'},
        {value: 17, label: '扫雷返还'},
        {value: 18, label: '扫雷佣金'},
        {value: 19, label: '扫雷赔付发包'},
        {value: 21, label: '接龙发包'},
        {value: 22, label: '接龙领包'},
        {value: 23, label: '接龙领包奖励'},
        {value: 24, label: '接龙佣金'},
        {value: 25, label: '领包冻结'},
        {value: 26, label: '领包解冻'},
        {value: 27, label: '牛牛下注'},
        {value: 28, label: '牛牛领包'},
        {value: 29, label: '牛牛赔付'},
        {value: 30, label: '牛牛佣金'},
        {value: 31, label: '牛牛返还'},
        {value: 32, label: '平台上分'},
        {value: 33, label: '平台下分'}
      ],
      list: [],
      totalNum: 0,
      dialogFormVisible: false,
      dialogData: {}
    }
  },
  methods: {
    handleSizeChange (val) { // 每页条数改变
      this.requestData.pageSize = val
      this._userUserAccountInfoList()
    },
    handleCurrentChange (val) { // page改变
      this.requestData.pageNum = val
      this._userUserAccountInfoList()
    },
    search () {
      this.requestData.pageNum = 1
      this._userUserAccountInfoList()
    },
    refresh () {
      this.requestData = {pageNum: 1, pageSize: 20, userName: '', infoType: ''}
      this._userUserAccountInfoList()
    },
    async _userUserAccountInfoList () {
      const res = await userUserAccountInfoList(this.requestData)
      if (res.result === '0') {
        this.list = res.datas.list
        this.totalNum = res.datas.totalRow
      }
    },
    detailsFn (row) {
      this.dialogFormVisible = true
      this.dialogData = row
    }
  },
  created () {
    this._userUserAccountInfoList()
  }
}
</script>

<style scoped lang="less">
.con{
  height: 100%;
  .log-date{
    font-size: 14px;
  }
  .my-table{
    height: calc(100% - 75px);
  }
}
.plan-con{
  >div{
    margin: 10px 0;
  }
  p{
    font-size: 14px;
    line-height: 30px;
    label{
      display: inline-block;
      width: 130px;
    }
    img{
      width: 100px;
      vertical-align: top;
    }
    button{
      margin-left: 30px;
    }
  }
  .project-box{
    border-bottom: 1px solid #eeeeee;
  }
}
</style>



